.ob1-push {
  height: 100%;
  background-color: transparent;
  border: 0;

  &:not(.ob1-push-zigzag):not(.ob1-push-no-interaction) {
    @extend .composite-link;

    .card-header {
      @include on-hover-n-focus-visible($black, $white, $gray2);
    }
  }

  .card-header:only-child {
    height: 100%;
  }

  .card-header {
    padding: 0 0 px-to-rem(5);
    margin: $push-spacer $push-spacer 0 0;
    color: $black;
    background-color: $gray2;
    border: none;

    .card-label {
      @extend .small;
    }

    .card-label-lead {
      @extend .lead;
      font-weight: $font-weight-bold;
    }

    .card-label,
    .card-label-lead {
      @include text-truncate();
      padding-right: $push-spacer;
      padding-left: $push-spacer;
      margin-bottom: -($spacer * .5);
    }

    .card-text,
    .card-text-light {
      @extend .standard;
      width: 100%;
      padding-right: $push-spacer;
      padding-left: $push-spacer;
      margin-bottom: 0;
      font-weight: $font-weight-bold;
      // deprecated - start
      a {
        display: inline-block;
        padding: 0;
      }
      // deprecated - end
    }

    .card-text-light {
      font-weight: $font-weight-light;

      a {
        font-weight: $font-weight-light;
      }
    }
  }

  .card-header > a,
  .card-item > a {
    display: block;
    padding: 0;
  }

  .card-img-top {
    margin-top: -($push-spacer);
    margin-left: $push-spacer;
  }

  .card-img-top:only-child {
    margin-bottom: px-to-rem(5);
  }

  .card-body {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: $push-spacer-md 0;

    .card-title {
      @extend .h4;
      max-width: px-to-rem(600);
      padding-bottom: 0;
      margin-bottom: $h3-padding-bottom-mobile;
    }

    .card-text {
      max-width: px-to-rem(600);
      margin-bottom: ($spacer * .5);
      color: $gray3;
    }

    .card-btn {
      margin-top: auto;
      text-align: center;
    }
  }

  @include media-breakpoint-up(sm) {
    .card-body {
      .card-btn {
        text-align: left;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .card-header {
      padding-bottom: px-to-rem(5);
      margin-top: $push-spacer-md;
      margin-right: $push-spacer-md;

      .card-label,
      .card-label-lead,
      .card-title,
      .card-text,
      .card-text-light {
        padding-right: $push-spacer-md;
        padding-left: $push-spacer-md;
      }

      .card-img-top {
        margin-top: -($push-spacer-md);
        margin-bottom: px-to-rem(5);
        margin-left: $push-spacer-md;
      }
    }

    .card-img-top:only-child {
      margin-bottom: $push-spacer;
    }

    .card-body {
      margin-bottom: ($push-spacer-md * 2);

      .card-title {
        margin-bottom: $h3-padding-bottom;
      }

      .card-btn {
        margin-bottom: $push-spacer-md;
      }
    }
  }

  @include media-breakpoint-up(xl) {
    .card-header {
      padding-bottom: $push-spacer;

      .card-img-top {
        margin-bottom: $push-spacer;

        &:only-child {
          margin-bottom: px-to-rem(5);
        }
      }
    }

    .card-body {
      .card-title {
        margin-bottom: $h3-padding-bottom-widescreen;
      }
    }
  }
}

// Push position décroché et header
// ==========================================================================
@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    $card-spacer: map_get($push-spacers, $breakpoint);

    .ob1-push.ob1-push#{$infix}-shadow-left,
    .ob1-push.ob1-push#{$infix}-header {
      @include shadow-position(right, #{$card-spacer});
    }

    .ob1-push.ob1-push#{$infix}-shadow-right,
    .ob1-push.ob1-push#{$infix}-header-right {
      @include shadow-position(left, #{$card-spacer});
    }

    @if $infix == "" {
      @include media-breakpoint-up(md) {
        .ob1-push.ob1-push-shadow-left,
        .ob1-push.ob1-push#{$infix}-header {
          @include shadow-position(right, #{$push-spacer-md});
        }

        .ob1-push.ob1-push-shadow-right,
        .ob1-push.ob1-push#{$infix}-header-right {
          @include shadow-position(left, #{$push-spacer-md});
        }
      }
    }

    .ob1-push.ob1-push#{$infix}-header {
      .card-header {
        display: block;

        .card-item {
          flex: none;
        }

        .card-img-top {
          width: 100%;
        }
      }
    }

    .ob1-push.ob1-push#{$infix}-header-right {
      .card-header {
        display: flex;

        .card-item {
          flex: 1 1;
        }

        .card-img-top {
          width: calc(100% + #{$card-spacer});
          @if $infix == "" or $infix == "sm" {
            margin-bottom: px-to-rem(5);
          }
        }
      }
    }
  }
}

// Push couleur
// ==========================================================================
.ob1-push.ob1-push-white {
  .card-header {
    background-color: $white;
    @include on-hover-n-focus-visible($black, $white);
  }
}

.ob1-push.ob1-push-dark {
  .card-header {
    background-color: $black;
    @include on-hover-n-focus-visible($white, $black);
  }
}

.ob1-push.ob1-push-gray1 {
  .card-header {
    background-color: $gray1;
    @include on-hover-n-focus-visible($white, $black, $gray1);
  }
}

.ob1-push.ob1-push-gray1,
.ob1-push.ob1-push-dark {
  .card-header {
    color: $white;

    a {
      color: $white;
    }

    &:not(.ob1-push-no-interaction) {
      @include on-hover-n-focus-visible($white, $black);
    }
  }
}

// Push zigzag
// ==========================================================================
.ob1-push.ob1-push-zigzag {
  .card-header {
    height: auto;
  }

  .card-img-top {
    margin-bottom: px-to-rem(5);
  }

  .card-body .card-title {
    font-size: $h3-font-size-mobile;
  }
}

@include media-breakpoint-up(md) {
  .ob1-push-zigzag {
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: ($spacer * 1.5);

    .card-item {
      flex: 1 1;
      align-self: center;
      padding: 0 ($grid-gutter-width / 2);

      &:first-child {
        align-self: flex-start;
      }

      .card-img-top {
        margin-bottom: $push-spacer;
      }

      .card-body .card-title {
        font-size: $h3-font-size;
      }
    }

    .card-body {
      display: block;
      margin: 0;

      .card-btn {
        margin-bottom: 0;
      }
    }
  }

  .card.ob1-push-zigzag-odd {
    flex-flow: row-reverse;
  }
}

@include media-breakpoint-up(lg) {
  $num: percentage(1 / $grid-columns);

  .ob1-push-zigzag-odd,
  .ob1-push-zigzag-even {
    .card-body {
      width: percentage(4 / ($grid-columns / 2));
      padding-left: calc(#{$num} + (#{$grid-gutter-width} / 2));

      .card-title {
        font-size: $h3-font-size;
      }
    }
  }
}

@include media-breakpoint-up(xl) {
  .ob1-push-zigzag {
    .card-item {
      .card-img-top {
        margin-bottom: px-to-rem(5);
      }

      .card-body .card-title {
        font-size: $h3-font-size-widescreen;
      }
    }
  }
}
