.ob1-rating {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  .ob1-score {
    padding-top: 0;
    padding-bottom: 0;
    padding-left: px-to-rem(5);
    @extend .small;
    color: $gray3;
  }

  .ob1-stars {
    position: relative;
    overflow: hidden;
  }

  .ob1-stars::before {
    font-size: px-to-rem(22);
    color: $gray4;
    white-space: nowrap;
    content: "★★★★★";
  }

  .ob1-stars::after {
    position: absolute;
    left: 0;
    overflow: hidden;
    font-size: px-to-rem(22);
    color: $primary;
    white-space: nowrap;
    content: "★★★★★";
  }

  @for $i from 0 through 50 {
    $value: $i / 10;
    .ob1-stars[data-rating^="#{$value}"]::after {
      width: $i * 2%;
    }
  }
}
