.ui-datepicker a:not([href]):not([tabindex]):hover,
.ui-datepicker a:not([href]):not([tabindex]):focus {
  color: $black;
}

.ui-datepicker a:not([href]):not([tabindex]):active {
  color: $white;
}

.ui-datepicker .ui-datepicker-prev,
.ui-datepicker .ui-datepicker-next {
  border-radius: px-to-rem(3);

  &::before {
    font-family: icon-sosh, sans-serif;
  }
}

.ui-datepicker .ui-datepicker-prev-hover,
.ui-datepicker .ui-datepicker-next-hover {
  top: 0;
  color: $black;
  border-color: $black;
  border-width: px-to-rem(2);
}

.ui-datepicker .ui-datepicker-prev-hover:active,
.ui-datepicker .ui-datepicker-next-hover:active {
  color: $white;
  background-color: $black;
}

.ui-state-active,
.ui-widget-content .ui-state-active,
.ui-widget-header .ui-state-active,
.ui-button:active,
.ui-button.ui-state-active:hover {
  color: $white;
  background-color: $smagenta1;
  border-color: $smagenta1;
  border-radius: px-to-rem(3);
}

.ui-datepicker td a:focus,
.ui-datepicker td a:hover {
  color: $black;
  border: px-to-rem(2) solid $black;
  border-radius: px-to-rem(3);
}

.ui-state-active.ui-state-hover,
.ui-datepicker td a:active {
  color: $white !important; // stylelint-disable-line declaration-no-important
  background-color: $black;
}
