// stylelint-disable declaration-no-important
h2:not(.no-signature):not(.h1):not(.h3):not(.h4):not(.h5):not(.h6):not(.lead):not(.small),
.h2:not(.no-signature) {
  color: $sblue1 !important;
}

h2::after,
.h2::after {
  display: none !important;
}

h2,
.h2 {
  padding-bottom: $h2-padding-bottom;
}
@include media-breakpoint-down(sm) {
  h2,
  .h2 {
    padding-bottom: $h2-padding-bottom-mobile;
  }
}
@include media-breakpoint-up(xl) {
  h2,
  .h2 {
    padding-bottom: $h2-padding-bottom-widescreen;
  }
}

// stylelint-enable declaration-no-important
