.ob1-promotional-code {
  display: flex;
  flex-wrap: wrap;

  .ob1-promotional-code-info {
    flex-basis: 100%;

    .alert {
      max-width: px-to-rem(613); // 450px input + 15px margin + 148px bouton
    }
  }

  .ob1-promotional-code-content {
    position: relative;
    width: 100%;
    max-width: px-to-rem($medium-form-group-max-width);

    .alert {
      margin-bottom: 0;
    }
  }

  .ob1-add-promotional-code {
    display: none;
    width: px-to-rem(148); // correspond à la taille d'un bouton
    margin-top: px-to-rem(20);
    margin-left: px-to-rem(15);

    &.ob1-loading {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  .ob1-cancel-promotional-code {
    display: flex;
    align-items: center;
    justify-content: center;
    width: px-to-rem(148); // correspond à la taille d'un bouton

    .icon {
      padding-top: px-to-rem(3);
      font-size: px-to-rem(12);
    }
  }
}

@include media-breakpoint-down(md) {
  .ob1-promotional-code {
    flex-direction: column;
    margin-bottom: px-to-rem(15);

    .ob1-add-promotional-code {
      margin-top: px-to-rem(30);
      margin-left: px-to-rem(0);
    }

    .ob1-cancel-promotional-code {
      margin-top: px-to-rem(0);
    }
  }
}
