// stylelint-disable declaration-no-important
@mixin override-bg-sosh($parent, $color) {
  .bg-secondary.bg-#{$parent},
  .bg-primary.bg-#{$parent},
  .bg-gray1.bg-#{$parent},
  .bg-gray2.bg-#{$parent},
  .bg-gray3.bg-#{$parent},
  .bg-gray4.bg-#{$parent} {
    background-color: $color !important;
  }
}

@mixin override-text-sosh($parent, $color) {
  .text-#{$parent} {
    color: $color !important;
  }
}

// stylelint-enable declaration-no-important
