.separator {
  height: px-to-rem(1);
  margin: 0; // l'espacement minimum est de 15 px, il dépend des marges des containers que le séparateur sépare
  border: 0;

  &--gray1 {
    background-color: $gray1;
  }
  &--gray2 {
    background-color: $gray2;
  }
  &--gray3 {
    background-color: $gray3;
  }
  &--gray4 {
    background-color: $gray4;
  }
  &--white {
    background-color: $white;
  }
  &--black {
    background-color: $black;
  }
  &--primary {
    background-color: $primary;
  }
  &--primary2 {
    background-color: $primary2;
  }

  &--large {
    height: ($spacer * .5);
  }
}
