.breadcrumb {
  flex-wrap: nowrap;
  max-width: $text-max-width;

  .breadcrumb-item {
    font-size: $font-size-transverse2;
    line-height: $line-height-transverse2;
    text-overflow: ellipsis;
    white-space: nowrap;

    &:last-child {
      overflow: hidden;
      font-weight: $font-weight-normal;
    }

    a {
      @extend .o-link;
    }

    a:not(:hover):not(:focus):not(:active),
    a:focus:not(:focus-visible):not(:hover) {
      text-decoration: none;
    }

    // pour supprimer le chevron quand l'élément précédént est masqué
    // la classe d-none va être rajouté par le JS breadcrumb.js
    &.sr-only + .breadcrumb-item {
      padding-left: 0;

      &::before {
        display: none;
      }
    }
  }

  &.ob1-breadcrumb-inverse {
    .breadcrumb-item {
      &,
      &::before {
        color: $white;
      }

      a {
        @extend .o-link-reverse;
      }
    }
  }
}

// deprecated - start
.bg-dark,
.bg-gray1 {
  .breadcrumb .breadcrumb-item {
    a {
      @extend .o-link-reverse;
    }

    &::before {
      color: $white;
    }
  }
}
