.custom-radio {
  .custom-control-label {
    padding-top: px-to-rem(2);
  }

  .custom-control-input,
  .custom-control-input:active {
    ~ .custom-control-label::before {
      content: $icon-radio-button-non;
    }

    &:hover,
    &:focus {
      &:not(:disabled) ~ .custom-control-label::before {
        color: $black;
        content: $icon-radio-button-non-bold;
      }
    }

    &:focus:not(:focus-visible):not(:hover) {
      &:not(:disabled) ~ .custom-control-label::before {
        color: $primary;
        content: $icon-radio-button-non;
      }
    }

    &:active:not(:disabled) ~ .custom-control-label::before {
      color: $black;
      background-color: $black;
    }

    &:checked {
      ~ .custom-control-label::before {
        color: $primary;
        content: $icon-radio-button-oui;
      }

      &:hover,
      &:focus {
        ~ .custom-control-label::before {
          color: $black;
          content: $icon-radio-button-oui;
        }
      }

      &:focus:not(:focus-visible):not(:hover) {
        ~ .custom-control-label::before {
          color: $primary;
          content: $icon-radio-button-oui;
        }
      }

      &:active ~ .custom-control-label::before {
        background-color: transparent;
      }
    }

    &:disabled ~ .custom-control-label::before {
      color: $gray4 !important; // stylelint-disable-line declaration-no-important
      background-color: transparent;
    }
  }

  &.ob1-custom-radio-inverse {
    .custom-control-input,
    .custom-control-input:active {
      &:hover,
      &:focus {
        ~ .custom-control-label::before { // stylelint-disable-line selector-max-class
          color: $white;
        }
      }

      &:focus:not(:focus-visible):not(:hover) {
        ~ .custom-control-label::before {
          color: $primary2;
        }
      }

      &:active:not(:disabled) ~ .custom-control-label::before {
        color: $white;
        background-color: $white;
      }

      &:checked {
        ~ .custom-control-label::before {
          color: $primary2;
        }

        &:hover,
        &:focus {
          ~ .custom-control-label::before {
            color: $white;
          }
        }

        &:focus:not(:focus-visible):not(:hover) {
          ~ .custom-control-label::before {
            color: $primary2;
          }
        }

        &:active ~ .custom-control-label::before {
          background-color: transparent;
        }
      }
    }
  }
}
