.btn-hour {
  border-radius: px-to-rem(3);

  &:focus,
  &:hover {
    color: $black;
    border: px-to-rem(2) solid $black;
  }

  &:active {
    color: $white;
    background-color: $black;
  }
}

.date-time-picker .selected {
  color: $white;
  background-color: $smagenta1;
  border-color: $smagenta1;

  &:hover,
  &:focus,
  &:active {
    color: $white;
    background-color: $black;
    border-color: $black;
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: $white;
    background-color: $primary;
    border-color: $primary;
  }
}

.date-time-picker .swiper-button-prev,
.date-time-picker .swiper-button-next {
  border-radius: px-to-rem(3);

  &:focus,
  &:hover {
    top: px-to-rem(26);
    color: $black;
    border: px-to-rem(2) solid $black;
  }

  &:active {
    color: $white;
    background-color: $black;
  }
}
