.items-list-item {
  &:focus {
    outline: px-to-rem(2) solid $primary;
  }

  .ob1-item-step {
    &::before {
      font-weight: $font-weight-bold;
      line-height: $line-height-transverse;
      border-width: px-to-rem(2);
    }

    &::after {
      position: absolute;
      top: px-to-rem(21);
      left: px-to-rem(38);
      width: px-to-rem(4);
      height: px-to-rem(4);
      content: "";
      background: $white;
      border-radius: 50%;
    }
  }

  .item-container {
    &:not(.item-loading)::after {
      font-family: icon-sosh, sans-serif;
      color: $primary;
    }
  }

  .item-desc-price {
    color: $primary;
  }

  &:hover,
  &:focus {
    .item-container,
    .item-icon {
      color: $black;
    }

    .item-text {
      text-decoration: none;

      .item-desc {
        text-decoration: underline;
        text-decoration-color: $gray3;
      }

      span {
        text-decoration: underline;
        text-decoration-color: $black;
      }
    }
  }

  &:focus:not(:focus-visible):not(:hover) {
    .item-text {
      .item-desc,
      span {
        text-decoration: none;
      }
    }
  }

  &:active {
    .item-icon,
    .item-container,
    .item-desc {
      color: $primary;
    }

    .item-text {
      .item-desc,
      span {
        color: $primary;
        text-decoration-color: $primary;
      }
    }
  }

  &.highlight {
    .item-icon,
    .item-icon::before {
      color: $primary !important; // stylelint-disable-line declaration-no-important
    }
    .item-text span {
      color: $primary;
    }
  }
}
