.ob1-push {
  $spacing-compact: px-to-rem(10) !default;
  $spacing-regular: px-to-rem(15) !default;

  @include sosh-push-colors($gray2, $primary);

  .card-header {
    display: flex;
    flex-direction: column;
    margin: 0;

    // double class in order to beat specificity for color versions on orange component
    &.card-header {
      background-color: transparent !important; // stylelint-disable-line declaration-no-important

      &,
      a {
        color: inherit !important; // stylelint-disable-line declaration-no-important
      }
    }

    &:hover,
    &:focus {
      .card-text-light::after {
        margin-right: 0;
      }
    }

    .card-label,
    .card-label-lead {
      position: relative;
      padding: $spacing-compact $spacing-compact px-to-rem(2) $spacing-compact;
      margin-top: px-to-rem(-34);
      margin-bottom: 0;
      border-width: px-to-rem(3) px-to-rem(3) 0 px-to-rem(3);
    }

    .card-text,
    .card-text-light {
      flex-grow: 2;
      padding: 0 $spacing-compact $spacing-compact $spacing-compact;
      border-width: 0 px-to-rem(3)  px-to-rem(3);
    }

    .card-text-light {
      &::after {
        display: block;
        margin-right: px-to-rem(10);
        font-family: icon-sosh, sans-serif;
        font-size: px-to-rem(30);
        line-height: .7;
        text-align: right;
        content: $icon-fleche-bloc;
        transition: margin .5s ease-out;
      }
    }

    .card-label,
    .card-label-lead,
    .card-text,
    .card-text-light {
      width: auto;
      margin-left: $spacing-compact;
      border-color: transparent;
      border-style: solid;
    }
  }

  .card-img-top {
    width: calc(100% - #{$spacing-compact});
    margin: 0;
  }

  @include media-breakpoint-up(md) {
    .card-header {
      .card-label,
      .card-label-lead {
        padding-right: $spacing-regular;
        padding-left: $spacing-regular;
        margin-top: px-to-rem(-24);
      }

      .card-text-light,
      .card-text {
        padding-right: $spacing-regular;
        padding-left: $spacing-regular;
      }

      .card-label-lead,
      .card-label,
      .card-text-light,
      .card-text {
        margin-left: $spacing-regular;
      }

      .card-img-top {
        width: calc(100% - #{$spacing-regular});
      }
    }
  }

  @include media-breakpoint-up(xl) {
    .card-header {
      .card-label-lead,
      .card-label {
        padding-top: $spacing-regular;
      }
    }
  }
}

.ob1-push.ob1-push-zigzag {
  .card-header .card-img-top {
    width: 100%;
  }
}

// Push couleur
// ==========================================================================
.ob1-push.ob1-push-white {
  @include sosh-push-colors($white, $primary);
}

.ob1-push.ob1-push-gray2 {
  @include sosh-push-colors($gray2, $primary);
}

.ob1-push.ob1-push-dark {
  @include sosh-push-colors($black, $primary2);
}

.ob1-push.ob1-push-gray1 {
  @include sosh-push-colors($gray1, $primary2);
}

.ob1-push.ob1-push-dark,
.ob1-push.ob1-push-gray1 {
  .card-label-lead,
  .card-label,
  .card-text-light,
  .card-text {
    color: $white;
  }
}
