.offer-tile, // deprecated
.ob1-offer-tile {
  border: px-to-rem(3) solid $gray4;

  &.white-offer-tile, // deprecated
  &.ob1-white-offer-tile {
    &:hover,
    &:focus,
    &.focus-within {
      color: $black;
      background-color: $white;
      border-color: $primary;
    }

    &:focus-within {
      color: $black;
      background-color: $white;
      border-color: $primary;
    }

    &:active {
      border-color: $black;
    }

    a {
      &:hover,
      &:focus,
      &:active {
        color: $black;
      }
    }

    &:active .ob1-price .ob1-price-by-month,
    &:active .ob1-price .ob1-price-from {
      color: $black;
    }

    &:active .ob1-price .ob1-price-promo-duration {
      color: $gray3;
    }

    &.focus-within:not(:focus-visible):not(:hover) {
      .ob1-price .ob1-price-amount { // stylelint-disable-line selector-max-class
        color: $primary;
      }
    }

    &:focus-within:not(:focus-visible):not(:hover) {
      .ob1-price .ob1-price-amount {
        color: $primary;
      }
    }
  }

  &.focus-within .price .price-amount, // deprecated
  &.focus-within .ob1-price .ob1-price-amount,
  &:hover .price .price-amount, // deprecated
  &:hover .ob1-price .ob1-price-amount {
    color: $primary;
  }

  &:focus-within .price .price-amount, // deprecated
  &:focus-within .ob1-price .ob1-price-amount {
    color: $primary;
  }

  &.ob1-offer-tile-selected {
    color: $white;
    background-color: $primary;
    border-color: $primary;

    .price .price-amount, // deprecated
    .ob1-price .ob1-price-amount {
      color: $white;
    }

    &:hover,
    &:focus,
    &.focus-within {
      // stylelint-disable selector-max-class
      .price .price-amount, // deprecated
      .ob1-price .ob1-price-amount {
        color: $black;
      }
    }

    &:focus-within {
      .price .price-amount, // deprecated
      .ob1-price .ob1-price-amount {
        color: $black;
      }
      // stylelint-enable selector-max-class
    }

    &.focus-within:not(:focus-visible):not(:hover) {
      color: $white;
      background-color: $primary;
      border-color: $primary;

      // stylelint-disable selector-max-class
      a,
      .ob1-price .ob1-price-amount {
        color: $white;
      }

      .ob1-price .ob1-price-by-month,
      .ob1-price .ob1-price-from {
        color: $black;
      }
      // stylelint-enable selector-max-class
    }

    &:focus-within:not(:focus-visible):not(:hover) {
      color: $white;
      background-color: $primary;
      border-color: $primary;

      a,
      .ob1-price .ob1-price-amount {
        color: $white;
      }

      .ob1-price .ob1-price-by-month,
      .ob1-price .ob1-price-from {
        color: $black;
      }
    }
  }

  &.ob1-offer-tile-disabled {
    a:focus {
      color: $white;
    }
  }

  .tag, // deprecated
  .ob1-tag {
    top: px-to-rem(-3);
    left: px-to-rem(-3);
  }
}
