.ob1-switch-view {
  .btn-check {
    + .btn {
      color: $primary;

      &:hover,
      &:focus {
        color: $white;
        background-color: $black;
      }

      &:focus:not(:focus-visible):not(:hover) {
        background-color: $primary;
      }

      &:active {
        color: $primary;
        background-color: $white;
      }
    }

    &:focus + .btn {
      outline-color: $primary;
    }

    &:checked {
      + .btn {
        color: $white;
        background-color: $primary;

        &:hover,
        &:active {
          color: $white;
          background-color: $black;
        }
      }

      &:focus-visible + .btn {
        color: $white;
        background-color: $black;
      }
    }
  }
}
