a {
  &:active,
  &:hover,
  &:focus {
    color: $primary;
  }

  &:focus {
    outline-color: $primary;
  }
}

.o-link-reverse {
  &:focus {
    outline-color: $primary2;
  }
}

.o-link {
  &:hover,
  &:focus {
    color: $black;
    text-decoration-color: $black;
  }

  &:active {
    color: $primary;
    text-decoration-color: $primary;
  }

  &.no-decoration {
    &:focus:not(:focus-visible):not(:hover) {
      color: $primary;
    }
  }

  &.o-link-reverse {
    &:hover,
    &:focus {
      color: $white;
    }

    &:active {
      color: $primary2;
      text-decoration-color: $primary2;
    }
  }
}

// stylelint-disable selector-no-qualifying-type
button.ob1-link-icon {
  &:focus {
    outline-color: $primary;
  }
}
// stylelint-enable selector-no-qualifying-type

.o-link-arrow {
  &,
  &::after,
  &::before,
  &.back::before {
    color: $primary;
  }

  &:hover,
  &:focus {
    color: $black;
    text-decoration-color: $black;
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: $primary;
  }

  &:active {
    color: $primary;
    text-decoration-color: $primary;
  }

  &.up,
  &.down {
    color: $black;

    &:hover,
    &:focus {
      text-decoration-color: $black;
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $black;
    }

    &:active {
      color: $primary;
      text-decoration-color: $primary;
    }
  }

  &.disabled {
    text-decoration-color: $gray4 !important; // stylelint-disable-line declaration-no-important
  }

  &.o-link-reverse {
    &,
    &::after,
    &::before {
      color: $primary2;
    }

    &:hover,
    &:focus {
      color: $white;
      text-decoration-color: $white;
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $primary2;
    }

    &:active {
      color: $primary2;
      text-decoration-color: $primary2;
    }

    &.up,
    &.down {
      color: $white;

      &:hover,
      &:focus {
        text-decoration-color: $white;
      }

      &:focus:not(:focus-visible):not(:hover) {
        color: $white;
      }

      &:active {
        color: $primary2;
        text-decoration-color: $primary2;
      }
    }

    &.disabled {
      text-decoration-color: $gray3 !important; // stylelint-disable-line declaration-no-important
    }
  }
}

.bg-gray2 {
  .o-link {
    color: $black;
    text-decoration-color: $black;

    &:focus:not(:focus-visible):not(:hover) {
      color: $black;
    }

    &:active {
      color: $primary;
      text-decoration-color: $primary;
    }
  }
}

.ob1-link-icon {
  &:hover,
  &:focus {
    color: $black;

    .ob1-link-icon-text {
      text-decoration-color: $black;
    }
  }

  &:active,
  &.focus-within:hover {
    color: $primary;

    .ob1-link-icon-text {
      text-decoration-color: $primary;
    }
  }

  &.o-link-reverse {
    &:hover,
    &:focus {
      color: $white;

      .ob1-link-icon-text {
        text-decoration-color: $white;
      }
    }

    &:active,
    &.focus-within:hover {
      color: $primary2;

      .ob1-link-icon-text {
        text-decoration-color: $primary2;
      }
    }
  }
}
