.accordion {
  .card {
    &.disabledCard { // stylelint-disable-line selector-class-pattern
      border-bottom: 1px solid $gray4;

      a,
      button,
      .accordion-icon {
        color: $gray4;
      }
    }
  }

  .card-header {
    // stylelint-disable selector-no-qualifying-type
    a,
    button {
      &:hover,
      &:focus {
        .accordion-title {
          color: $black;
        }
      }

      // &:focus:not(:focus-visible):not(:hover) .accordion-title {
      //   color: $primary;
      // }

      &:active {
        .accordion-title {
          color: $primary;
        }
      }
    }
    // stylelint-enable selector-no-qualifying-type

    button {
      &:focus {
        outline: px-to-rem(2) solid $primary;
      }
    }
  }

  .accordion-card-title {
    .accordion-icon {
      color: $primary;

      &::before {
        font-family: icon-sosh, sans-serif;
        font-weight: $font-weight-normal;
      }
    }
  }
}

.ob1-accordion-inverse {
  .accordion-card-title .accordion-icon {
    color: $primary2;
  }

  .card-header {
    a,
    button {
      &:focus {
        outline: px-to-rem(2) solid $primary2;
      }
      &:hover,
      &:focus {
        color: $white;

        .accordion-title {
          color: $white;
        }
      }
      &:active {
        color: $primary2;

        .accordion-title {
          color: $primary2;
        }
      }
    }
  }
}
