.date-title {
  margin-bottom: px-to-rem(10);
  font-size: $font-size-transverse;
  font-weight: $font-weight-bold;
  color: $black;
  text-align: center;
  vertical-align: middle;
}

.btn-hour {
  display: inline-block;
  width: px-to-rem(68);
  height: px-to-rem(35);
  font-size: .8rem;
  line-height: .8rem;
  color: $black;
  text-align: center;
  vertical-align: middle;
  background-color: transparent;
  border: 0;
  border-radius: 0;

  &:focus,
  &:hover {
    font-weight: $font-weight-bold;
    color: $gray3;
    border: px-to-rem(1) solid $gray3;
  }

  &:active {
    border-width: px-to-rem(2);
  }
}

.date-time-picker .selected {
  font-weight: $font-weight-bold;
  border: px-to-rem(2) solid $primary;

  &:hover,
  &:focus,
  &:active {
    color: $gray3;
    border-color: $gray3;
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: $black;
    border-color: $primary;
  }
}

.hour-disable {
  color: $gray4;
  pointer-events: none;
}

.date-time-picker .swiper-button-prev,
.date-time-picker .swiper-button-next {
  top: px-to-rem(28);
  width: px-to-rem(38);
  height: px-to-rem(28);
  padding-top: px-to-rem(2);
  color: $black;
  text-align: center;
  text-shadow: none;
  cursor: pointer;
  background-color: $white;
  background-image: none;
  background-size: 0;

  &:focus,
  &:hover {
    top: px-to-rem(27);
    color: $gray3;
    border: px-to-rem(1) solid $gray3;
  }

  &:active {
    top: px-to-rem(26);
    color: $gray3;
    border-width: px-to-rem(2);
  }

  &::after {
    content: none;
  }
}

.date-time-picker .swiper-button-next.swiper-button-disabled,
.date-time-picker .swiper-button-prev.swiper-button-disabled {
  color: $gray4;
  pointer-events: none;
  cursor: auto;
  background-color: $white;
  opacity: 1;
}

.date-time-picker .swiper-button-prev::before,
.date-time-picker .swiper-button-next::before {
  font-size: $font-size-transverse;
}

.ob1-date-time-picker-day-times {
  max-width: px-to-rem(450);
}

.ob1-date-time-picker-slots {
  .btn-hour {
    width: px-to-rem(140);
  }
}

// Small screens
@include media-breakpoint-up(sm) {
  .btn-hour {
    width: px-to-rem(68);
    font-size: $font-size-transverse2;
    line-height: $font-size-transverse2;
  }
}

// Medium screens and UP
@include media-breakpoint-up(md) {
  .date-title {
    max-width: px-to-rem(110);
  }

  .date-time-picker {
    width: 100%;
  }

  .date-title.disabled {
    color: $gray4;
  }

  .date-time-picker .swiper-button-prev,
  .date-time-picker .swiper-button-next {
    display: block;
  }

  .btn-hour {
    display: block;
    width: px-to-rem(110);
    height: px-to-rem(50);
    font-size: $font-size-transverse2;
    line-height: $font-size-transverse2;
  }

  .ob1-date-time-picker-day-times {
    max-width: none;
  }

  .ob1-date-time-picker-arrow {
    width: px-to-rem(48);
  }

  .ob1-date-time-picker-slots {
    .date-title {
      max-width: px-to-rem(140);
    }
  }
}
