.breadcrumb {
  .breadcrumb-item {
    a {
      &:hover,
      &:focus {
        color: $black;
        text-decoration-color: $black;
      }

      &:active {
        color: $primary;
        text-decoration-color: $primary;
      }
    }

    &::before {
      /* use !important to prevent issues with browser extensions that change fonts */
      font-family: icon-sosh, sans-serif !important; // stylelint-disable-line declaration-no-important
    }
  }

  &.ob1-breadcrumb-inverse {
    .breadcrumb-item {
      a {
        &:hover,
        &:focus {
          color: $white;
          text-decoration-color: $white;
        }

        &:active {
          color: $primary2;
          text-decoration-color: $primary2;
        }
      }
    }
  }
}

