.pagination {
  align-items: center;
  justify-content: center;
}

.page-link {
  font-size: $font-size-transverse;
  font-weight: $font-weight-normal;
  text-align: center;
  outline-offset: px-to-rem(4);
  transition: outline-offset .15s ease-in-out;

  &:focus {
    border-color: transparent;
    outline: $pagination-padding-x solid $primary;
    outline-offset: 0;
  }

  &:hover,
  &:focus {
    padding: px-to-rem(7) px-to-rem(3);
    font-weight: $font-weight-bold;
    border: px-to-rem(1) solid $gray3;
  }

  &:focus:not(:focus-visible):not(:hover) {
    font-weight: $font-weight-normal;
    border-color: transparent;
  }
}

.page-item {
  &:first-child {
    .page-link {
      &::before {
        top: px-to-rem(11);
        left: px-to-rem(12);
        width: px-to-rem(8);
        height: px-to-rem(12);
      }

      &:hover,
      &:focus {
        &::before {
          top: px-to-rem(12);
          left: .813rem;
          background-image: #{$pagination-icon-prev-hover};
        }
      }

      &:focus:not(:focus-visible):not(:hover) {
        &::before {
          background-image: #{$pagination-icon-prev-active};
        }
      }

      &:active {
        &::before {
          top: px-to-rem(11);
          left: px-to-rem(12);
          background-image: #{$pagination-icon-prev-hover};
        }
      }
    }
  }

  &:last-child {
    .page-link {
      &::before {
        top: px-to-rem(11);
        left: px-to-rem(14);
        width: px-to-rem(8);
        height: px-to-rem(12);
      }

      &:hover,
      &:focus {
        &::before {
          top: px-to-rem(12);
          left: px-to-rem(15);
          background-image: #{$pagination-icon-next-hover};
        }
      }

      &:focus:not(:focus-visible):not(:hover) {
        &::before {
          background-image: #{$pagination-icon-next-active};
        }
      }

      &:active {
        &::before {
          top: px-to-rem(11);
          left: px-to-rem(14);
          background-image: #{$pagination-icon-next-hover};
        }
      }
    }
  }

  &:first-child,
  &:last-child {
    .page-link {
      &:focus {
        border-color: transparent;
      }

      &:hover,
      &:focus {
        border: px-to-rem(1) solid $gray3;
      }

      &:focus:not(:focus-visible):not(:hover) {
        border-color: transparent;
      }

      &:active {
        border: $pagination-border-width solid $gray3;
      }
    }
  }

  &:nth-child(2):last-child {
    margin-left: (.5 * $spacer);
  }

  &:first-child:nth-last-child(2) {
    margin-right: (.5 * $spacer);

    .o-link-arrow.back {
      margin-right: 0;
    }
  }

  .page-link:active {
    padding: $pagination-padding-y $pagination-padding-x;
    font-weight: $font-weight-bold;
    color: $pagination-hover-color;
    text-align: center;
    border-width: $pagination-border-width;
  }

  &.active .page-link {
    padding: $pagination-padding-y $pagination-padding-x;
    font-weight: $font-weight-bold;
    text-align: center;
    border: solid $pagination-border-width $primary2;

    &:hover,
    &:focus {
      color: $pagination-hover-color;
      border: $pagination-border-width solid $gray3;
    }

    &:focus:not(:focus-visible):not(:hover) {
      font-weight: $font-weight-bold;
      color: $black;
      border-color: $primary;
    }
  }

  &.active {
    &.disabled .page-link {
      border: $pagination-border-width solid $gray4;

      &:hover,
      &:focus {
        border-color: $gray4 !important; // stylelint-disable-line declaration-no-important
      }
    }
  }

  &.ob1-page-item-ellipsis {
    .page-link {
      &:hover,
      &:focus,
      &:active {
        pointer-events: initial;
        cursor: default;
        border-color: transparent;
      }
    }
  }

  &.ob1-page-item-counter {
    min-width: px-to-rem(52);
    padding: px-to-rem(7) 0 px-to-rem(10);
    text-align: center;
  }

  .o-link-arrow:not(.back) {
    margin-left: (.5 * $spacer);
  }

  .o-link-arrow.back {
    margin-right: (.5 * $spacer);
  }

  &.disabled .page-link {
    font-weight: $font-weight-bold !important; // stylelint-disable-line declaration-no-important
    pointer-events: initial;
    cursor: not-allowed;
    border-width: $pagination-border-width;

    &:hover,
    &:focus,
    &:active {
      padding: $pagination-padding-y $pagination-padding-x;
      color: $gray4 !important; // stylelint-disable-line declaration-no-important
      border: $pagination-border-width solid transparent;
    }
  }

  &.disabled:first-child .page-link {
    @include hover-focus () {
      &::before {
        top: px-to-rem(11);
        left: px-to-rem(12);
        background-image: #{$pagination-icon-prev-disabled} !important; // stylelint-disable-line declaration-no-important
      }
    }
  }

  &.disabled:last-child .page-link {
    @include hover-focus () {
      &::before {
        top: px-to-rem(11);
        left: px-to-rem(14);
        background-image: #{$pagination-icon-next-disabled} !important; // stylelint-disable-line declaration-no-important
      }
    }
  }

  + .page-item {
    margin-left: px-to-rem(4);
  }

  @include media-breakpoint-down(sm) {
    display: none;

    &:first-child,
    &:last-child,
    &:nth-child(n+2):nth-child(-n+6) {
      display: list-item;
    }
  }

  @include media-breakpoint-only(sm) {
    display: none;

    &:first-child,
    &:last-child,
    &:nth-child(n+2):nth-child(-n+8) {
      display: list-item;
    }
  }

  @include media-breakpoint-only(md) {
    display: none;

    &:first-child,
    &:last-child,
    &:nth-child(n+2):nth-child(-n+12) {
      display: list-item;
    }
  }

  @include media-breakpoint-only(lg) {
    display: none;

    &:first-child,
    &:last-child,
    &:nth-child(n+2):nth-child(-n+16) {
      display: list-item;
    }
  }

  @include media-breakpoint-up(xl) {
    display: none;
    &:first-child,
    &:last-child,
    &:nth-child(n+2):nth-child(-n+20) {
      display: list-item;
    }
  }
}
