.ob1-page-indicator {
  display: inline-flex;
  height: px-to-rem(30);
  padding-left: 0;
  margin: 0;
  vertical-align: bottom;
  list-style: none;

  .ob1-page-indicator-item {
    display: flex;
    align-items: flex-end;
    width: px-to-rem(60);
    padding-bottom: 15px;
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }

    &:nth-last-child(n+3),
    &:nth-last-child(n+3) ~ .ob1-page-indicator-item {
      width: px-to-rem(45);

      &:last-child {
        margin-right: 0;
      }
    }

    &:nth-last-child(n+4),
    &:nth-last-child(n+4) ~ .ob1-page-indicator-item {
      width: px-to-rem(35);
      margin-right: 10px;
    }

    &:nth-last-child(n+5),
    &:nth-last-child(n+5) ~ .ob1-page-indicator-item {
      width: px-to-rem(30);
    }
  }

  .ob1-page-indicator-button {
    width: 100%;
    height: px-to-rem(3);
    padding: 0;
    background-color: $dark;
    border: 0;
    transition: .15s;

    &:hover {
      height: px-to-rem(6);
      transition: .3s;
    }

    &:active {
      background-color: $primary;
      transition: none;
    }
  }

  .ob1-page-indicator-active {
    .ob1-page-indicator-button {
      height: px-to-rem(6);
      background-color: $primary;
    }
  }

  &.ob1-page-indicator-inverse {
    .ob1-page-indicator-button {
      background-color: $white;

      &:active {
        background-color: $primary2;
      }
    }

    .ob1-page-indicator-active {
      .ob1-page-indicator-button {
        background-color: $primary2;
      }
    }
  }

  &.ob1-page-indicator-neutral {
    .ob1-page-indicator-button {
      background-color: $gray4;

      &:active {
        background-color: $primary2;
      }
    }

    .ob1-page-indicator-active {
      .ob1-page-indicator-button {
        background-color: $primary2;
      }
    }
  }
}
