.custom-checkbox {
  .custom-control-input,
  .custom-control-input:active {
    &:checked,
    &:indeterminate {
      ~ .custom-control-label::before {
        color: $primary;
      }

      &:active:not(:disabled) ~ .custom-control-label::before {
        color: $black;
      }
    }

    &:hover,
    &:focus,
    &:active {
      ~ .custom-control-label::before {
        color: $black;
      }
    }

    &:hover,
    &:focus {
      &:not(:checked):not(:indeterminate):not(:disabled) ~ .custom-control-label::before {
        content: $icon-checkbox-non-bold;
      }
    }

    &:focus:not(:focus-visible):not(:hover) {
      ~ .custom-control-label::before {
        color: $black;
      }

      &:not(:checked):not(:indeterminate):not(:disabled) ~ .custom-control-label::before {
        content: $icon-checkbox-non;
      }

      &:checked ~ .custom-control-label::before {
        color: $primary;
      }
    }

    &:active:not(:checked):not(:indeterminate):not(:disabled) ~ .custom-control-label::before {
      content: $icon-checkbox-non-full;
    }

    &:disabled ~ .custom-control-label::before {
      color: $gray4;
    }
  }

  &.ob1-custom-checkbox-inverse {
    .custom-control-input,
    .custom-control-input:active {
      &:checked,
      &:indeterminate {
        ~ .custom-control-label::before {
          color: $primary2;
        }

        &:active:not(:disabled) ~ .custom-control-label::before {
          color: $white;
        }
      }

      &:hover,
      &:focus,
      &:active {
        ~ .custom-control-label::before {
          color: $white;
        }
      }

      &:focus:not(:focus-visible):not(:hover) {
        ~ .custom-control-label::before {
          color: $white;
        }

        &:checked ~ .custom-control-label::before {
          color: $primary2;
        }
      }

      &:disabled ~ .custom-control-label::before {
        color: $gray3;
      }
    }
  }
}
