//region old
.bs-tooltip-top {
  .arrow {
    // stylelint-disable declaration-no-important
    left: calc(50% - 1rem) !important;
    // stylelint-enable declaration-no-important
    &::before {
      border-width: ($tooltip-arrow-height * 1.5) ($tooltip-arrow-width * 1.5) 0;
    }
  }
}

.bs-tooltip-right {
  .arrow {
    // stylelint-disable declaration-no-important
    top: calc(50% - 1rem) !important;
    // stylelint-enable declaration-no-important
    &::before {
      border-width: ($tooltip-arrow-width * 1.5) ($tooltip-arrow-height * 1.5) ($tooltip-arrow-width * 1.5) 0;
    }
  }
}

.bs-tooltip-bottom {
  .arrow {
    // stylelint-disable declaration-no-important
    left: calc(50% - 1rem) !important;
    // stylelint-enable declaration-no-important
    &::before {
      border-width: 0 ($tooltip-arrow-width * 1.5) ($tooltip-arrow-height * 1.5);
    }
  }
}

.bs-tooltip-left {
  .arrow {
    // stylelint-disable declaration-no-important
    top: calc(50% - 1rem) !important;
    // stylelint-enable declaration-no-important

    &::before {
      border-width: ($tooltip-arrow-width * 1.5) 0 ($tooltip-arrow-width * 1.5) ($tooltip-arrow-height * 1.5);
    }
  }
}

// Wrapper for the tooltip content
.tooltip-inner {
  width: $tooltip-max-width;
  padding-top: $tooltip-padding-top;
  text-align: left;
}
//endregion

//Wrapper for the tooltip content
.ob1-tooltip {
  position: relative;

  .ob1-tooltip-content {
    display: none;
  }
}

.ob1-tooltip:hover {
  .ob1-tooltip-content {
    position: fixed;
    z-index: 10100;
    display: initial;
    padding: px-to-rem(15);
    margin: 0;
    font-size: $font-size-transverse2;
    background: $white;
  }

  //centering the tooltip when shown on mobile
  .ob1-tooltip-content.ob1-tooltip-top,
  .ob1-tooltip-content.ob1-tooltip-bottom,
  .ob1-tooltip-content.ob1-tooltip-right,
  .ob1-tooltip-content.ob1-tooltip-left {
    top: 25%;
    right: 5%;
    left: 5%;
  }

  .ob1-tooltip-content.ob1-tooltip-top {
    bottom: initial;
  }

  //rendering the tooltip shadow and hide it on click
  .ob1-tooltip-shadow {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10050;
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .5);

    &:active {
      z-index: -1;
    }
  }
}

@include media-breakpoint-up(md) {
  .ob1-tooltip {
    display: inline-flex;

    .ob1-tooltip-content {
      display: none;
    }
  }

  //reset the toltip positionment on desktop view
  .ob1-tooltip:hover {
    .ob1-tooltip-content.ob1-tooltip-top,
    .ob1-tooltip-content.ob1-tooltip-bottom,
    .ob1-tooltip-content.ob1-tooltip-right,
    .ob1-tooltip-content.ob1-tooltip-left {
      top: auto;
      right: auto;
      bottom: auto;
      left: auto;
    }

    .ob1-tooltip-content {
      position: absolute;
      z-index: 107;
      box-sizing: border-box;
      width: max-content;
      max-width: px-to-rem(380);
      padding: px-to-rem(15);
      box-shadow: 0 0 5px 0 #aaa;

      //add arrow to desktop tooltip
      &::after {
        position: absolute;
        display: initial;
        margin-left: -10px;
        content: "";
        border: 8px solid #000;
        border-color: transparent transparent #fff #fff;
        box-shadow: -3px 3px 3px 0 rgba(0, 0, 0, .15);
        transform-origin: -1px 0;
      }
    }

    .ob1-tooltip-content.ob1-tooltip-top {
      bottom: 100%;
      left: 1px;
      transform: translateY(-6%);

      &::after {
        top: 100%;
        left: 22px;
        transform: rotate(-45deg);
      }
    }

    .ob1-tooltip-content.ob1-tooltip-bottom {
      top: 100%;
      left: 1px;
      transform: translateY(6%);

      &::after {
        top: 1px;
        left: 40px;
        transform: rotate(135deg);
      }
    }

    .ob1-tooltip-content.ob1-tooltip-right {
      top: 1px;
      left: 100%;
      transform: translateX(6%);

      &::after {
        top: 10px;
        left: 12px;
        transform: rotate(45deg);
      }
    }

    .ob1-tooltip-content.ob1-tooltip-left {
      top: 1px;
      right: 100%;
      transform: translateX(-6%);

      &::after {
        top: 30px;
        right: -12px;
        transform: rotate(225deg);
        transform-origin: 1px 0;
      }
    }

    .ob1-tooltip-shadow {
      display: none;
    }
  }
}

// KEYBOARD NAVIGATION
