.ob1-quick-reply {
  display: inline-flex;
  align-items: center;
  padding: px-to-rem(5) px-to-rem(20) px-to-rem(7) !important; // stylelint-disable-line declaration-no-important
  margin: px-to-rem(6) 0;
  font-size: $font-size-transverse2;
  font-weight: $font-weight-bold;
  line-height: $line-height-transverse2;
  border: px-to-rem(1) solid $black;
  border-radius: px-to-rem(16) !important; // stylelint-disable-line declaration-no-important

  &:hover,
  &:focus {
    color: $gray3;
    border-color: $gray3;
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: $black;
    border-color: $black;
  }

  &:active {
    color: $primary;
    border-color: $primary;
  }

  &:disabled {
    color: $gray4 !important; // stylelint-disable-line declaration-no-important
    background-color: transparent !important; // stylelint-disable-line declaration-no-important
    border-color: $gray4 !important; // stylelint-disable-line declaration-no-important
  }

  .icon {
    &:only-child {
      height: px-to-rem(18);
      margin-top: px-to-rem(2);
      font-size: px-to-rem(18);
    }

    &:not(:last-child) {
      padding-right: px-to-rem(8);
      font-size: px-to-rem(16);
    }
  }
}
