.swiper-container,
.ob1-carrousel {
  position: relative;
}

.ob1-carrousel {
  .swiper-wrapper {
    padding-left: 0;
    margin-bottom: 0;
    list-style-position: inside;
    list-style-type: none;
  }

  .swiper-slide {
    width: 100%;

    &:focus {
      outline: px-to-rem(2) solid $primary;
      outline-offset: -2px;
    }

    .ob1-carrousel-img {
      width: 100%;
      min-height: 40%;
    }
  }

  .swiper-pagination {
    bottom: 0;
    left: 50%;
    width: auto;
    transform: translateX(-50%);
  }

  .swiper-button-next,
  .swiper-button-prev,
  .ob1-carrousel-controls {
    color: $white;
    background: rgba(0, 0, 0, .42);
    border: 0;

    &::before {
      font-size: 20px;
      text-shadow: none;
    }

    &:hover {
      background: rgba(0, 0, 0, .54);
    }
  }

  .ob1-carrousel-controls {
    position: absolute;
    bottom: 10px;
    left: 10px;
    z-index: 11;
    display: none;
    padding: 5px 9px;

    .icon {
      font-size: px-to-rem(12);
    }

    .ob1-link-icon-text {
      display: none;
    }

    &:active {
      padding-left: 9px;
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $white;
    }
  }

  .ob1-carrousel-controls-play {
    padding-right: 8px;
    padding-left: 10px;

    &:active {
      padding-left: 10px;
    }
  }

  .ob1-carrousel-controls-right {
    right: 10px;
    left: auto;
  }

  .swiper-button-next,
  .swiper-button-prev {
    top: 50%;
    padding: 6px;

    &::after {
      content: none;
    }
  }

  .swiper-button-prev {
    left: 0;
    padding-right: 15px;
    padding-left: 12px;
  }

  .swiper-button-next {
    right: 0;
    padding-right: 12px;
    padding-left: 15px;
  }

  &.ob1-carrousel-xsidebar {
    .swiper-slide {
      text-align: right;
    }

    .ob1-carrousel-img {
      max-width: 50%;
    }

    .ob1-carrousel-sidebar {
      position: absolute;
      top: 50%;
      left: ($spacer * .5);
      max-width: calc(50% - #{$spacer});
      text-align: left;
      transform: translateY(-50%);
    }
  }

  &.ob1-carrousel-xs-ysidebar {
    .ob1-carrousel-sidebar {
      max-height: 60%;
      padding: 0 ($spacer * .5) $spacer;
    }
  }

  @include media-breakpoint-up(sm) {
    &.ob1-carrousel-xsidebar,
    &.ob1-carrousel-xs-ysidebar {
      .swiper-slide {
        text-align: right;
      }

      .ob1-carrousel-img {
        min-width: 40%;
        max-width: none;
      }

      .ob1-carrousel-sidebar {
        max-width: calc(60% - #{$spacer});
        max-height: none;
      }
    }

    &.ob1-carrousel-xs-ysidebar {
      .ob1-carrousel-sidebar {
        position: absolute;
        top: 50%;
        left: ($spacer * .5);
        padding: 0 !important; // stylelint-disable-line declaration-no-important
        text-align: left;
        transform: translateY(-50%);
      }
    }
  }

  @include media-breakpoint-up(md) {
    &.ob1-carrousel-xsidebar,
    &.ob1-carrousel-xs-ysidebar {
      .ob1-carrousel-img {
        min-width: 60%;
      }

      .ob1-carrousel-sidebar {
        left: 60px;
        max-width: calc(40% - 60px);
      }

      .ob1-slide-inversion {
        text-align: left;

        .ob1-carrousel-sidebar {
          right: 60px;
          left: auto;
        }
      }
    }
  }
}

@include media-breakpoint-up(md) {
  .ob1-carrousel {
    .ob1-carrousel-controls {
      display: block;
    }

    .swiper-button-prev,
    .swiper-button-next {
      display: block;
    }
  }
}

// Spécificités de la déclinaison "Fiche produit"
.ob1-carrousel-product-sheet {
  text-align: center;

  .swiper-wrapper {
    @include media-breakpoint-between(xs, sm) {
      width: px-to-rem(320);
      height: px-to-rem(320);
    }
    @include media-breakpoint-between(sm, md) {
      width: px-to-rem(480);
      height: px-to-rem(480);
    }
    @include media-breakpoint-between(md, lg) {
      width: px-to-rem(504);
      height: px-to-rem(504);
    }
    @include media-breakpoint-between(lg, xl) {
      width: px-to-rem(450);
      height: px-to-rem(450);
    }
    @include media-breakpoint-up(xl) {
      width: px-to-rem(504);
      height: px-to-rem(504);
    }
  }

  .swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    .ob1-carrousel-product-sheet-picture {
      height: 100%;
      .ob1-carrousel-img {
        width: auto;
        max-width: 100%;
        max-height: 100%;
      }
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    color: $black;
    background: rgba(255, 255, 255, .54);
  }

  .ob1-page-indicator {
    position: relative;
    left: auto;
    max-width: px-to-rem(290);
    margin-top: px-to-rem(15);
    transform: none;

    .ob1-page-indicator-item {
      margin-right: px-to-rem(13);
    }

    .ob1-page-indicator-item:not(.ob1-page-indicator-active) {
      width: px-to-rem(15);
    }

    .ob1-page-indicator-item.ob1-page-indicator-active {
      width: px-to-rem(25);
    }
  }
}
