.modal-dialog {
  .modal-content {
    overflow: hidden;
  }

  .modal-header {
    .close:not(.o-link-arrow) {
      &:hover,
      &:focus {
        color: $primary;
      }

      &:active {
        color: $dark;
      }
    }
  }
}

@include media-breakpoint-up(md) {
  .modal-dialog {
    .modal-content {
      border-radius: px-to-rem(10);
    }
  }
}
