.ob1-filter-chips-bar {
  display: flex;
  flex-wrap: wrap;
  max-height: px-to-rem(49);
  padding-left: 0;
  list-style: none;

  .ob1-filterchip {
    .ob1-filterchip-label {
      margin-right: px-to-rem(15);
      margin-bottom: px-to-rem(15);

      &.ob1-filter-chips-bar-btn {
        outline-offset: px-to-rem(4);
        transition: outline-offset .15s ease-in-out;

        &:focus {
          outline: px-to-rem(2) solid $primary;
          outline-offset: 0;
        }

        &:hover,
        &:focus {
          font-weight: $font-weight-bold;
          color: $gray3;
          border-color: $gray3;
        }

        &:active {
          padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;
          border-width: px-to-rem(2);
        }
      }
    }

    &.ob1-filterchip-selected {
      .ob1-filter-chips-bar-btn {
        &:active {
          padding: $filterchip-selected-padding-top $filterchip-selected-padding-x $filterchip-selected-padding-bottom;
        }
      }
    }
  }
}

.modal-body {
  .ob1-filter-chips-bar {
    max-height: none;
  }

  .icon.icon-delete {
    font-size: px-to-rem(12);
  }
}
