.ob1-floating-action-btn {
  &::after {
    font-family: icon-sosh, sans-serif;
    color: $primary;
  }

  &:focus {
    outline-color: $primary;
  }

  &:hover,
  &:focus {
    &::after {
      color: $black;
    }
  }

  &:focus:not(:focus-visible):not(:hover) {
    &::after {
      color: $primary;
    }
  }

  &:active {
    &::after {
      color: $primary;
    }
  }
}
