.ob1-quick-reply {
  color: $primary;
  border-color: $primary;
  border-width: px-to-rem(2);

  &:hover,
  &:focus {
    color: $black;
    border-color: $black;
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: $primary;
    border-color: $primary;
  }

  &:active {
    color: $white;
    background-color: $primary;
    border-color: $primary;
  }
}
