.form-input-file {
  max-width: px-to-rem(450);

  // deprecated - start
  .btn-input.is-invalid ~ .invalid-feedback:not(.alert-container) {
    display: block;
    min-height: px-to-rem(38);
    padding: px-to-rem(8) px-to-rem(54) px-to-rem(10) px-to-rem(50);
    margin: 0 0 px-to-rem(15) 0;
    font-size: $font-size-transverse2;
    color: $black;
  }

  .btn-input.is-invalid ~ .invalid-feedback:not(.alert-container)::after {
    position: absolute;
    top: 50%;
    left: px-to-rem(15);
    width: auto;
    height: auto;
    font-family: icon-orange, sans-serif;
    font-size: px-to-rem(20);
    color: $red;
    content: $icon-error-severe;
    background-size: auto;
    transform: translateY(-50%);
  }

  .invalid-feedback-label {
    display: block;
    max-width: px-to-rem(372);
    padding-bottom: px-to-rem(3);
    overflow: hidden;
    font-weight: $font-weight-normal;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .invalid-feedback-error-message > span {
    display: block;
  }
  // deprecated - end

  .btn-input {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    margin-bottom: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
  }

  label.btn { // stylelint-disable-line selector-no-qualifying-type
    outline-offset: px-to-rem(4);
    transition: outline-offset .15s ease-in-out;
  }

  .btn-input.focus-visible ~ label.btn { // stylelint-disable-line selector-no-qualifying-type
    color: $gray3;
    border-color: $gray3;
    outline: px-to-rem(2) solid $primary;
    outline-offset: 0;
  }

  .btn-input:disabled ~ .attachment-format,
  .btn-input:disabled ~ .attachment-size,
  .btn-input:disabled ~ .attachment-files {
    color: $gray4;
  }

  .btn-input:disabled.full ~ .attachment-format,
  .btn-input:disabled.full ~ .attachment-size,
  .btn-input:disabled.full ~ .attachment-files {
    color: $gray3;
  }

  .attachment-format {
    padding-bottom: $spacer * .5;
    font-weight: $font-weight-bold;
  }

  .attachment-size {
    padding: 0;
  }

  .attachment-files {
    padding-bottom: $spacer * .5;
  }

  .attachment-files,
  .attachment-format,
  .attachment-size {
    padding-top: 0;
    margin-bottom: 0;
    font-size: $font-size-transverse2;
    color: $gray3;
  }

  .attachments {
    padding-left: 0;
    margin-bottom: $spacer * .5;
    list-style-type: none;

    .attachment {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      height: px-to-rem(70);
      padding-left: $spacer * .5;
      margin-bottom: $spacer * .5;
      font-size: $font-size-transverse;
      line-height: $line-height-transverse;
      background-color: $gray2;

      .file-name {
        @extend .o-link;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      .file-size {
        width: px-to-rem(118);
        text-align: right;
      }

      .btn-icon {
        width: px-to-rem(14);
        padding: 0;
        margin: 0 px-to-rem(15);
      }

      // deprecated - start
      > .icon-trash {
        width: px-to-rem(54);
        text-align: center;
        cursor: pointer;
        background-color: transparent;
        border: 0;
      }
      // deprecated - end
    }
  }

  .alert-container.invalid-feedback {
    margin-bottom: $spacer * .5;
  }
}
