.search-field-results {
  .result-line {
    &.result-line-focused {
      border-color: $primary;
    }

    &:hover,
    &:focus {
      color: $primary;
      text-decoration-color: $primary;
    }

    &:active {
      color: $black;
      text-decoration: underline;
      text-decoration-color: $black;
    }
  }
}
