.ui-datepicker a:not([href]):not([tabindex]):hover,
.ui-datepicker a:not([href]):not([tabindex]):focus {
  color: $gray3;
}

.ui-datepicker {
  display: none;
  padding: .2em .2em 0;
}

.ui-datepicker .ui-datepicker-header {
  position: relative;
  padding: .2em 0;
}

.ui-datepicker .ui-datepicker-prev,
.ui-datepicker .ui-datepicker-next {
  position: absolute;
  top: px-to-rem(2);
  left: auto;
  width: px-to-rem(38);
  height: px-to-rem(28);
  padding-top: px-to-rem(2);
  text-align: center;

  &::before {
    font-family: icon-orange, sans-serif;
    font-size: $font-size-transverse;
  }
}

.ui-datepicker .ui-datepicker-prev::before {
  content: $icon-arrow-previous;
}

.ui-datepicker .ui-datepicker-next::before {
  content: $icon-arrow-next;
}

.ui-datepicker .ui-datepicker-prev {
  left: px-to-rem(2);
}

.ui-datepicker .ui-datepicker-next {
  right: px-to-rem(2);
}

.ui-datepicker .ui-datepicker-prev-hover,
.ui-datepicker .ui-datepicker-next-hover {
  top: px-to-rem(1);
  color: $gray3;
  border: px-to-rem(1) solid $gray3;
}

.ui-datepicker .ui-datepicker-prev-hover:active,
.ui-datepicker .ui-datepicker-next-hover:active {
  top: 0;
  border-width: px-to-rem(2);
}

.ui-datepicker .ui-datepicker-prev span,
.ui-datepicker .ui-datepicker-next span {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  margin-top: -8px;
  margin-left: -8px;
}

.ui-datepicker .ui-datepicker-title {
  margin: 0 2.3em;
  font-size: $font-size-transverse;
  font-weight: $font-weight-bold;
  line-height: $line-height-transverse;
  color: $black;
  text-align: center;
  background: $white;
  border: 0;
}

.ui-datepicker .ui-datepicker-title select {
  margin: 1px 0;
  font-size: 1em;
}

.ui-datepicker select .ui-datepicker-month,
.ui-datepicker select .ui-datepicker-year {
  width: 45%;
}

.ui-datepicker table {
  width: 100%;
  margin: 0 0 .4em;
  font-size: $font-size-transverse;
  border-collapse: collapse;
}

.ui-datepicker th {
  padding: .7em .3em;
  font-size: $font-size-transverse;
  font-weight: $font-weight-normal;
  color: $gray3;
  text-align: center;
  border: 0;
}

.ui-datepicker td {
  padding: px-to-rem(1);
  border: 0;
}

.ui-datepicker td span,
.ui-datepicker td a {
  display: block;
  text-align: center;
  text-decoration: none;
}

.ui-datepicker .ui-datepicker-buttonpane {
  padding: 0 .2em;
  margin: .7em 0 0;
  background-image: none;
  border-right: 0;
  border-bottom: 0;
  border-left: 0;
}

.ui-datepicker .ui-datepicker-buttonpane button {
  float: right;
  width: auto;
  padding: .2em .6em .3em;
  margin: .5em .2em .4em;
  overflow: visible;
  cursor: pointer;
}

.ui-datepicker .ui-datepicker-buttonpane button .ui-datepicker-current {
  float: left;
}

/* with multiple calendars */
.ui-datepicker.ui-datepicker-multi {
  width: auto;
  padding: px-to-rem(20);
}

.ui-datepicker-multi .ui-datepicker-group {
  float: left;
}

.ui-datepicker-multi .ui-datepicker-group table {
  width: 95%;
  margin: 0 auto .4em;
}

.ui-datepicker-multi-2 .ui-datepicker-group {
  width: 50%;
  padding: px-to-rem(10);
}

.ui-datepicker-multi-3 .ui-datepicker-group {
  width: 33.3%;
  padding: px-to-rem(10);
}

.ui-datepicker-multi-4 .ui-datepicker-group {
  width: 25%;
  padding: px-to-rem(10);
}

.ui-datepicker-multi .ui-datepicker-group-last .ui-datepicker-header,
.ui-datepicker-multi .ui-datepicker-group-middle .ui-datepicker-header {
  border-left-width: 0;
}

.ui-datepicker-multi .ui-datepicker-buttonpane {
  clear: left;
}

.ui-datepicker-row-break {
  width: 100%;
  clear: both;
  font-size: 0;
}

/* RTL support */
.ui-datepicker-rtl {
  direction: rtl;
}

.ui-datepicker-rtl .ui-datepicker-prev {
  right: px-to-rem(2);
  left: auto;
}

.ui-datepicker-rtl .ui-datepicker-next {
  right: auto;
  left: px-to-rem(2);
}

.ui-datepicker-rtl .ui-datepicker-prev:hover {
  right: px-to-rem(1);
  left: auto;
}

.ui-datepicker-rtl .ui-datepicker-next:hover {
  right: auto;
  left: px-to-rem(1);
}

.ui-datepicker-rtl .ui-datepicker-buttonpane {
  clear: right;
}

.ui-datepicker-rtl .ui-datepicker-buttonpane button {
  float: left;
}

.ui-datepicker-rtl .ui-datepicker-buttonpane button .ui-datepicker-current,
.ui-datepicker-rtl .ui-datepicker-group {
  float: right;
}

.ui-datepicker-rtl .ui-datepicker-group-last .ui-datepicker-header,
.ui-datepicker-rtl .ui-datepicker-group-middle .ui-datepicker-header {
  border-right-width: 0;
  border-left-width: px-to-rem(1);
}

/* Icons */
.ui-datepicker .ui-icon {
  top: .3em;
  left: .5em;
  display: block;
  overflow: hidden;
  text-indent: -99999px;
  background-repeat: no-repeat;
}

.ui-widget.ui-widget-content {
  width: max-content;
  background-color: $white;
  border: 1px solid $gray4;
  border-top: 2px solid $black;
}

.ui-widget-content {
  border: 0;
}

.ui-datepicker .ui-widget-header {
  font-size: $font-size-transverse;
  font-weight: $font-weight-bold;
  line-height: $line-height-transverse;
  color: $black;
  background: $white;
  border: 0;
}

.ui-datepicker-calendar .ui-datepicker-week-end {
  background: $white;
}

.ui-datepicker-calendar .ui-datepicker-other-month {
  background: $white;
}

.ui-datepicker-calendar .ui-datepicker-unselectable {
  font-size: $font-size-transverse;
  color: $gray4;
  background: $white;
  border: 0;
}

.ui-datepicker .ui-datepicker-current-day {
  font-size: $font-size-transverse;
  font-weight: $font-weight-bold;
  color: $white;
  border: 0;
}

.ui-datepicker .ui-datepicker-today {
  font-size: $font-size-transverse;
  font-weight: $font-weight-bold;
  color: $black;
  background: $white;
  border: 0;
}

.ui-datepicker .ui-state-default {
  width: px-to-rem(38);
  height: px-to-rem(35);
  padding-top: px-to-rem(6);
  padding-bottom: px-to-rem(7);
  font-size: $font-size-transverse;
  line-height: $line-height-transverse;
  border: 0;
}

.ui-state-highlight,
.ui-widget-content .ui-state-highlight,
.ui-widget-header .ui-state-highlight {
  font-weight: $font-weight-bold;
}

.ui-state-active,
.ui-widget-content .ui-state-active,
.ui-widget-header .ui-state-active,
.ui-button:active,
.ui-button.ui-state-active:hover {
  font-size: $font-size-transverse;
  font-weight: $font-weight-bold;
  border: px-to-rem(2) solid $primary;
}

.ui-datepicker td a:focus,
.ui-datepicker td a:hover {
  font-weight: $font-weight-bold;
  color: $gray3;
  border: px-to-rem(1) solid $gray3;
}

.ui-state-active.ui-state-hover,
.ui-datepicker td a:active {
  border-width: px-to-rem(2) !important; // stylelint-disable-line declaration-no-important
}

// stylelint-disable selector-no-qualifying-type
a.ui-datepicker-prev.ui-corner-all.ui-state-disabled::before,
a.ui-datepicker-next.ui-corner-all.ui-state-disabled::before {
  color: $gray4;
}

a.ui-datepicker-prev.ui-state-disabled,
a.ui-datepicker-next.ui-state-disabled {
  pointer-events: none;
  cursor: auto;
  background-color: $white;
  opacity: 1;
}

.date-picker {
  /* stylelint-disable-next-line selector-class-pattern */
  .labelDatePicker + .icon {
    pointer-events: none;
  }
}
