.ob1-box-radio {
  position: relative;
  min-width: px-to-rem(290);
  max-width: px-to-rem(450);
  height: 100%;
  padding: 0;
  margin: 0;
  border: transparent px-to-rem(1) solid;
  border-bottom: px-to-rem(1) solid $gray4;

  &:hover,
  &:active {
    border-color: $gray3;
  }

  .item-text::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    content: "";
  }

  // deprecated - start
  label {
    margin: 0;
    font-weight: 400;
  }
  // deprecated end

  .items-list-item .item-container {
    justify-content: left;
    padding: px-to-rem(15);
    border: none;
  }

  // deprecated - start
  .ob1-box-radio-img {
    align-items: center;
    padding: ($spacer / 2) 0;
  }
  // deprecated end
}

.ob1-box-radio-selected {
  border: $primary px-to-rem(1) solid;
}
