.progressbar-label {
  font-weight: $font-weight-bold;
  color: $smagenta1;
}

// stylelint-disable selector-no-qualifying-type
progress[value] {
  /* For IE10 */
  color: $smagenta1;
  border-radius: 2.5px;

  &::-webkit-progress-value {
    background-color: $smagenta1;
    border-radius: 2.5px;
  }

  &::-moz-progress-bar {
    background-color: $smagenta1;
    border-radius: 2.5px;
  }
}
// stylelint-enable selector-no-qualifying-type
