@mixin updateItemsSosh($circle-width, $padding-top-step-number) {
  .step-link{
    &::before {
      padding-top: calc(#{$padding-top-step-number} + 2px);
    }

    &::after {
      top: calc(#{$padding-top-step-number} + 3px);
      font-size: $circle-width;
    }
  }

  &.active {
    .step-link::before {
      font-size: $circle-width;
    }
  }
}

.stepbar {
  .step-item {
    &::after {
      background-color: $gray4;
    }

    .step-link {
      &::before {
        color: $black;
        border-width: 0 !important; // stylelint-disable-line declaration-no-important
      }

      &::after {
        position: absolute;
        font-family: icon-sosh, sans-serif;
        content: $icon-circle;
      }

      .step-title {
        color: $black;
      }
    }

    &:last-child {
      .step-link {
        &::before {
          content: "";
        }

        &::after {
          content: $icon-Le-Vote;
        }

      }
    }

    &.active {
      .step-link {
        font-weight: $font-weight-normal;
        color: $primary-official;

        &::before {
          padding-top: 0;
          color: $primary-official;
          border: 0;
        }

        &::after {
          content: none;
        }

        &:hover,
        &:focus,
        &:active {
          &::before,
          &::after {
            color: $black;
          }
        }

        &:focus:not(:focus-visible):not(:hover) {
          &::before,
          &::after {
            color: $primary-official;
          }
        }
      }

      .step-title {
        font-weight: $font-weight-bold;
        color: $primary-official;
      }
    }

    &.complete {
      .step-title {
        color: $primary-official;
      }

      &::after {
        background-color: $primary-official;
      }

      .step-link {
        color: $primary-official;

        // stylelint-disable selector-max-class
        &:hover,
        &.focus-visible {
          &::before,
          &::after {
            font-weight: $font-weight-normal;
            color: $black;
          }
        }
        // stylelint-enable selector-max-class

        &:focus-visible {
          &::before,
          &::after {
            font-weight: $font-weight-normal;
            color: $black;
          }
        }

        &:active {
          &::before {
            color: $white;
            background-color: $black;
          }
        }
      }
    }
  }
}

// Extra small and Small screens
@include media-breakpoint-down(sm) {
  .stepbar {
    .step-item {
      @include updateItemsSosh($step-circle-width-sm, $step-padding-top-step-number-sm);

      &.complete {
        .step-link::before {
          color: $primary-official;
        }
      }
    }
  }
}

// Medium  et Large screens
@include media-breakpoint-up(md) {
  .stepbar {
    .step-item {
      @include updateItemsSosh($step-circle-width-md, $step-padding-top-step-number-md);

      &.complete {
        .step-link::before {
          color: $primary-official;
        }
      }
    }
  }
}

// Extra large et XX large screens
@include media-breakpoint-up(xl) {
  .stepbar {
    .step-item {
      @include updateItemsSosh($step-circle-width-xl, $step-padding-top-step-number-xl);
    }
  }
}
