@mixin manageItems($circle-width, $margin-x-circle, $margin-bottom-circle, $padding-top-step-number) {

  // one or two item
  &:first-child:nth-last-child(1),
  &:first-child:nth-last-child(2),
  &:first-child:nth-last-child(2) ~ .step-item:not(:last-child) {
    width: 100%;
  }

  // three items
  &:first-child:nth-last-child(3),
  &:first-child:nth-last-child(3) ~ .step-item:not(:last-child) {
    width: calc((100% - #{ceil($circle-width)}) / 2);
  }

  // four items
  &:first-child:nth-last-child(4),
  &:first-child:nth-last-child(4) ~ .step-item:not(:last-child) {
    width: calc((100% - #{ceil($circle-width)}) / 3);
  }

  // five items
  &:first-child:nth-last-child(5),
  &:first-child:nth-last-child(5) ~ .step-item:not(:last-child) {
    width: calc((100% - #{ceil($circle-width)}) / 4);
  }

  &:last-child {
    width: $circle-width;
  }

  &::after {
    top: ($circle-width / 2);
    margin-left: ($circle-width / 2) + $margin-x-circle;
  }

  .step-link {
    &::before {
      width: $circle-width;
      height: $circle-width;
      padding-top: $padding-top-step-number;
      margin-bottom: $margin-bottom-circle;
      box-shadow: -$margin-x-circle 0 0 0 $white, $margin-x-circle 0 0 0 $white;
    }
  }

  &:not(:first-child):not(:last-child) .step-title {
    margin-left: calc(-50% + #{$circle-width / 2});
  }
}

.stepbar {
  ol,
  ul {
    list-style: none;
  }

  ol {
    padding-left: 0;
    margin-bottom: 0;
    counter-reset: step;
  }

  .step-item {
    position: relative;
    z-index: 1;
    float: left;
    font-size: $font-size-mobile;
    line-height: 1.125rem;

    &::after {
      position: absolute;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 1px;
      content: "";
      background-color: $gray4;
    }

    .step-link {
      display: block;
      pointer-events: none;

      &::before {
        display: block;
        color: $gray3;
        text-align: center;
        content: counter(step);
        counter-increment: step;
        background-color: $white;
        border: 1px solid $gray4;
        border-radius: 100%;
      }

      .step-title {
        @extend .standard;
        display: block;
        padding: 0;
        color: $gray3;
        white-space: nowrap;
      }
    }

    &:first-child {
      text-align: left;
    }

    &:last-child {
      text-align: right;

      &::after {
        content: none;
      }

      .step-link {
        display: flex;
        flex-direction: column;

        &::before {
          font-family: icon-orange, sans-serif !important; // stylelint-disable-line declaration-no-important
          content: $icon-tick;
        }

        .step-title {
          align-self: flex-end;
        }
      }
    }

    &:not(:first-child):not(:last-child) .step-title {
      width: 100%;
      text-align: center;
    }

    &.active {
      .step-link {
        font-weight: $font-weight-bold;

        &::before {
          color: $black;
          border-color: $primary;
          border-width: 2px;
        }

        &:hover,
        &:focus,
        &:active {
          &::before {
            color: $gray3;
            border-color: $gray3;
            // border-width: 2px;
          }
        }

        &:focus:not(:focus-visible):not(:hover)::before {
          color: $black;
          border-color: $primary;
        }
      }

      .step-title {
        color: $black;
      }
    }

    &.complete {
      &::after {
        background-color: $black;
      }

      .step-link {
        &::before {
          color: $black;
          border-color: $black;
        }

        &:hover,
        &:focus,
        &:active {
          &::before {
            font-weight: $font-weight-bold;
            color: $gray3;
            border-color: $gray3;
          }
        }

        &:focus:not(:focus-visible):not(:hover)::before {
          font-weight: $font-weight-normal;
          color: $black;
          border-color: $black;
        }

        &:active {
          &::before {
            border-width: 2px;
          }
        }
      }

      .step-title {
        color: $black;
      }
    }
  }
}

// Extra small and Small screens
@include media-breakpoint-down(sm) {
  .stepbar {
    ol {
      padding-bottom: 47px;
    }

    .step-item {
      @include manageItems($step-circle-width-sm, $step-margin-x-circle-sm, $step-margin-bottom-circle-sm, $step-padding-top-step-number-sm);
      line-height: 1.25rem;

      &::after {
        pointer-events: none;
      }

      .step-link {
        display: block;
        pointer-events: none;
      }

      &:not(.active) {
        .step-title {
          display: none;
        }
      }

      &.complete {
        .step-link::before {
          font-size: $font-size-min;
        }
      }
    }
  }
}

// Medium  et Large screens
@include media-breakpoint-up(md) {
  .stepbar {
    ol {
      padding-bottom: 62px;
    }

    .step-item {
      @include manageItems($step-circle-width-md, $step-margin-x-circle-md, $step-margin-bottom-circle-md, $step-padding-top-step-number-md);
      .step-link {
        &::before {
          font-size: $font-size-base;
        }
      }

      &.complete,
      &.active {
        .step-link {
          pointer-events: visible;
        }
      }
    }
  }
}

// Extra large et XX large screens
@include media-breakpoint-up(xl) {
  .stepbar {
    ol {
      padding-bottom: 84px;
    }

    .step-item {
      @include manageItems($step-circle-width-xl, $step-margin-x-circle-xl, $step-margin-bottom-circle-xl, $step-padding-top-step-number-xl);
      font-size: $font-size-base;

      .step-link {
        &::before {
          font-size: $font-size-base * 1.25;
        }
      }
    }
  }
}
