.ob1-timeline {
  $step-big-size: px-to-rem(36);
  $step-medium-size: px-to-rem(24);
  $step-small-size: px-to-rem(14);
  $timeline-margin-bottom: px-to-rem(15);
  $timeline-padding-top: px-to-rem(6);
  $timeline-padding-bottom: px-to-rem(10);

  $inactive-color: $gray4;
  $active-color: $green;

  @mixin progress-percentage($step-size) {
    &.progress-33 {
      &::before {
        top: $step-size;
        height: calc(.33 * (100% + #{$timeline-margin-bottom} + #{$timeline-padding-top} - #{$step-size}));
      }
    }

    &.progress-66 {
      &::before {
        top: $step-size;
        height: calc(.66 * (100% + #{$timeline-margin-bottom} + #{$timeline-padding-top} - #{$step-size}));
      }
    }
  }

  position: relative;
  padding-left: px-to-rem(45);
  list-style: none;

  &::before {
    position: absolute;
    top: px-to-rem(6);
    left: px-to-rem(15);
    display: inline-block;
    height: calc(100% - #{$timeline-padding-top} - #{$timeline-padding-bottom});
    content: "";
    border-left: px-to-rem(2) solid $inactive-color;
  }

  /*
   * Definition of common properties step & sub-steps
   */
  &-step,
  &-sub-step {
    position: relative;
    @extend .text-mw;

    .ob1-timeline-title {
      @extend .lead;
    }

    &:not(.active):not(.complete) {
      .ob1-timeline-title,
      .ob1-timeline-description {
        color: $gray3;
      }
    }

    &::before {
      position: absolute;
      top: $timeline-padding-top;
      left: px-to-rem(-30);
      display: inline-block;
      width: px-to-rem(10);
      height: calc(100% + #{$timeline-margin-bottom});
      content: "";
    }

    &:last-child {
      margin-bottom: 0;

      &::before {
        top: px-to-rem(10);
        border-left: px-to-rem(2) solid $white;
      }
    }

    &::after {
      position: absolute;
      top: $timeline-padding-top;
      display: inline-block;
      color: $white;
      text-align: center;
      content: "";
      background-color: $inactive-color;
      border-radius: 50%;
    }

    &.complete {
      &::before {
        margin-top: px-to-rem(5);
        border-left: px-to-rem(2) solid $active-color;
      }

      &::after {
        font-size: px-to-rem(10);
        background-color: $active-color;
        border: px-to-rem(2) solid $active-color;
      }
    }

    &.active {
      &::before {
        border-left: px-to-rem(2) solid $active-color;
      }

      &::after {
        font-family: icon-orange, sans-serif;
        color: $active-color;
        content: $icon-tick-circle;
        background-color: $white;
      }
    }
  }

  /*
   * Step properties
   */
  &-step {
    .ob1-timeline-title {
      font-weight: $font-weight-bold;
    }

    .ob1-timeline-description {
      padding-top: 0;
      margin-top: px-to-rem(-25);
    }

    &::after {
      left: px-to-rem(-41);
      width: $step-medium-size;
      height: $step-medium-size;
    }

    &.active {
      $step-top-offset: px-to-rem(5);
      @include progress-percentage($step-big-size);

      &::before {
        top: $step-big-size;
        height: calc(.5 * (100% + #{$timeline-margin-bottom} + #{$timeline-padding-top} - #{$step-big-size}));
      }

      &::after {
        top: 0;
        left: px-to-rem(-47);
        width: $step-big-size;
        height: $step-big-size;
        font-size: $step-big-size;
        line-height: $step-big-size;
      }
    }
  }

  /*
   * Sub-step properties
   */
  &-sub-step {
    $step-top-offset: px-to-rem(6);

    .ob1-timeline-description {
      margin-bottom: 0;
    }

    &::after {
      left: px-to-rem(-36);
      width: $step-small-size;
      height: $step-small-size;
      margin-top: $step-top-offset;
    }

    &.complete {
      @include progress-percentage($step-small-size);
    }

    &.active {
      &::before {
        top: px-to-rem(26);
        height: px-to-rem(12);
      }

      &::after {
        font-size: $step-small-size;
        line-height: $step-small-size;
      }
    }
  }

  .ob1-timeline-sub-step + .ob1-timeline-step {
    .ob1-timeline-title {
      margin-top: $timeline-margin-bottom;
    }
  }

  .ob1-timeline-step + .ob1-timeline-sub-step {
    &.complete,
    &.active {
      &::before {
        top: -50%;
        height: 200%;
      }
    }
  }
}
