.ob1-bubble {
  max-width: px-to-rem(640);

  .ob1-bubble-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: px-to-rem(10);

    .ob1-bubble-header-icon {
      width: px-to-rem(24);
      height: px-to-rem(24);
      padding: px-to-rem(6);
      margin-right: px-to-rem(10);
      font-size: px-to-rem(13);
      background-color: $gray2;
      border-radius: 50%;
    }

    .ob1-bubble-header-title {
      font-size: $font-size-transverse2;
      line-height: $line-height-transverse2;
      color: $gray3;
    }
  }

  .ob1-bubble-body {
    .ob1-bubble-content {
      width: fit-content;
      padding: px-to-rem(10) px-to-rem(20);
      margin: 0 0 px-to-rem(3);
      font-size: $font-size-transverse2;
      line-height: $line-height-transverse2;
      background-color: $gray2;
      border-radius: px-to-rem(20);

      &:nth-last-child(n+2) {
        border-radius: px-to-rem(20) px-to-rem(20) px-to-rem(20) 0;
      }

      &:nth-last-child(n+3) + .ob1-bubble-content {
        border-radius: 0 px-to-rem(20) px-to-rem(20) 0;
      }

      &:nth-last-child(n+2) ~ .ob1-bubble-content:last-child {
        border-radius: 0 px-to-rem(20) px-to-rem(20) px-to-rem(20);
      }
    }
  }

  .ob1-bubble-footer {
    margin-bottom: px-to-rem(15);
    font-size: $font-size-transverse3;
    line-height: $line-height-transverse3;
    color: $gray3;
  }

  &.ob1-bubble-chatbot {
    .ob1-bubble-header-icon {
      padding: 0;
      font-size: px-to-rem(24);
      color: $primary;
    }
  }

  &.ob1-bubble-right {
    text-align: right;

    .ob1-bubble-header {
      justify-content: flex-end;

      .ob1-bubble-header-title {
        align-items: flex-start;
        text-align: right;
      }
    }

    .ob1-bubble-body {
      display: flex;
      flex-direction: column;
      align-items: flex-end;

      // stylelint-disable selector-max-class
      .ob1-bubble-content {
        display: inline-block;
        color: $white;
        background-color: $gray1;

        &:nth-last-child(n+2) {
          border-radius: px-to-rem(20) px-to-rem(20) 0;
        }

        &:nth-last-child(n+3) + .ob1-bubble-content {
          border-radius: px-to-rem(20) 0 0 px-to-rem(20);
        }

        &:nth-last-child(n+2) ~ .ob1-bubble-content:last-child {
          border-radius: px-to-rem(20) 0 px-to-rem(20) px-to-rem(20);
        }
      }
    }
  }
}
