@each $color, $value in $sosh-alert-button-colors {
  .alert-container.alert-#{$color} .alert-content .btn-secondary:active {
    color: $value;
    background-color: $black;
  }
}

.alert {
  .alert-icon {
    font-family: icon-sosh, sans-serif;
  }
}

.alert-success .alert-icon::before {
  content: $icon-tick-circle;
}

.alert-danger .alert-icon::before {
  content: $icon-error-severe;
}

.alert-warning .alert-icon::before {
  content: $icon-Warning-important;
}

.alert-info .alert-icon ::before {
  content: $icon-info;
}

.alert-container:not(.additional-message) {
  .alert-content {
    .btn.btn-secondary {
      color: $black;
      background-color: transparent;
      border-color: $black;
    }

    .btn.btn-secondary:hover,
    .btn.btn-secondary:focus {
      background-color: $white;
      border: solid 2px $white;
    }

    // deprecated
    .o-link-arrow:not(.disabled) {
      color: $black;
      text-decoration-color: $black;

      &::after {
        color: $black;
      }

      &.down {
        &:hover,
        &:focus {
          color: $black;
          text-decoration-color: $black;
        }
      }
    }
  }
}

.alert-container {
  &.ob1-notification-message {
    a {
      .alert::after {
        font-family: icon-sosh, sans-serif;
      }
    }
  }
}
