.form-input-file {
  .btn-input.focus-visible + label.btn { // stylelint-disable-line selector-no-qualifying-type
    color: $black;
    border-color: $black;
    outline-color: $primary;
  }

  .btn-input.is-invalid ~ .invalid-feedback::before {
    color: $black;
  }

  .btn-input.is-invalid ~ .invalid-feedback::after {
    font-family: icon-sosh, sans-serif;
  }
}
