.page-link {
  font-weight: 500;
  color: $dark;
  background-color: $white;
  border-radius: px-to-rem(3);

  &:hover,
  &:focus {
    padding: $pagination-padding-y $pagination-padding-x;
    font-weight: $font-weight-bold;
    color: $dark;
    background-color: $white;
    border-color: $dark;
    border-width: $pagination-border-width;
  }
}

.page-item {
  &:first-child {
    .page-link {
      &::before {
        background-image: #{$pagination-icon-prev-active};
      }

      &:active {
        &::before {
          background-image: #{$pagination-icon-prev-active-inverse};
        }
      }

      &:hover,
      &:focus {
        &::before {
          top: px-to-rem(11);
          left: px-to-rem(12);
        }
      }
    }
  }

  &:last-child {
    .page-link {
      &::before {
        background-image: #{$pagination-icon-next-active};
      }

      &:active {
        &::before {
          background-image: #{$pagination-icon-next-active-inverse};
        }
      }

      &:hover,
      &:focus {
        &::before {
          top: px-to-rem(11);
          left: px-to-rem(14);
        }
      }
    }
  }

  &:first-child,
  &:last-child {
    .page-link {
      &:hover,
      &:focus {
        border-color: $dark;
        border-width: $pagination-border-width;
      }
    }
  }

  .page-link:active {
    font-weight: $font-weight-bold;
    color: $white;
    background-color: $dark;
    border-color: $dark;
  }

  &.active .page-link {
    color: $white;
    background-color: $primary;
    border-color: $primary;

    &:hover,
    &.focus {
      background-color: $dark;
      border-color: $dark;
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $white;
      border-color: $primary;
    }
  }

  &.active {
    &.disabled .page-link {
      color: $white !important; // stylelint-disable-line declaration-no-important
      background-color: $gray4;
      border-color: $gray4;

      &:hover,
      &:focus {
        color: $white;
        background-color: $gray4;
      }
    }
  }

  &.ob1-page-item-ellipsis .page-link {
    &:hover,
    &:focus,
    &:active {
      color: $dark;
      background-color: transparent;
      border-color: transparent;
    }
  }

  &.disabled .page-link {
    font-weight: 500;

    &:hover,
    &:focus,
    &:active {
      color: $gray4;
      background-color: transparent;
      border-color: transparent;
    }
  }

  &.disabled:first-child .page-link::before {
    background-image: #{$pagination-icon-prev-disabled};
  }

  &.disabled:last-child .page-link::before {
    background-image: #{$pagination-icon-next-disabled};
  }

  &.disabled:first-child,
  &.disabled:last-child {
    .page-link {
      @include hover-focus () {
        border-color: transparent;
      }
    }
  }
}
