.offer-tile, // deprecated
.ob1-offer-tile {
  border: px-to-rem(1) solid $black;

  // Tuile noire
  &.black-offer-tile, // deprecated
  &.ob1-black-offer-tile {
    color: $white;
    background-color: $black;

    &.focus-within,
    &:hover {
      background-color: $gray3;
      border-color: $gray3;
    }

    &:focus-within {
      background-color: $gray3;
      border-color: $gray3;
    }

    &.focus-within:not(:focus-visible):not(:hover) {
      color: $white;
      background-color: $black;
      border-color: $black;

      // stylelint-disable selector-max-class
      a,
      .ob1-price .ob1-price-by-month,
      .ob1-price .ob1-price-from {
        color: $white;
      }

      .ob1-price .ob1-price-amount {
        color: $primary2;
      }

      .ob1-price .ob1-price-promo-duration {
        color: $gray4;
      }
      // stylelint-enable selector-max-class
    }

    &:focus-within:not(:focus-visible):not(:hover) {
      color: $white;
      background-color: $black;
      border-color: $black;

      // stylelint-disable selector-max-class
      a,
      .ob1-price .ob1-price-by-month,
      .ob1-price .ob1-price-from {
        color: $white;
      }

      .ob1-price .ob1-price-amount {
        color: $primary2;
      }

      .ob1-price .ob1-price-promo-duration {
        color: $gray4;
      }
      // stylelint-enable selector-max-class
    }

    &:active {
      color: $black;
      background-color: $white;
      border-color: $primary;
    }

    &:active .ob1-price .ob1-price-by-month,
    &:active .ob1-price .ob1-price-from {
      color: $black;
    }

    &:active .ob1-price .ob1-price-promo-duration {
      color: $gray3;
    }
  }

  // Tuile blanche
  &.white-offer-tile, // deprecated
  &.ob1-white-offer-tile {
    color: $black;
    background-color: $white;
    border-color: $gray4;

    &.focus-within,
    &:hover {
      border-color: $primary;
    }

    &:focus-within {
      border-color: $primary;
    }

    &.focus-within:not(:focus-visible):not(:hover) {
      color: $black;
      background-color: $white;
      border-color: $gray4;

      // stylelint-disable selector-max-class
      a,
      .ob1-price .ob1-price-by-month,
      .ob1-price .ob1-price-from {
        color: $black;
      }

      .ob1-price .ob1-price-amount {
        color: $primary;
      }

      .ob1-price .ob1-price-promo-duration {
        color: $gray3;
      }
      // stylelint-enable selector-max-class
    }

    &:focus-within:not(:focus-visible):not(:hover) {
      color: $black;
      background-color: $white;
      border-color: $gray4;

      // stylelint-disable selector-max-class
      a,
      .ob1-price .ob1-price-by-month,
      .ob1-price .ob1-price-from {
        color: $black;
      }

      .ob1-price .ob1-price-amount {
        color: $primary;
      }

      .ob1-price .ob1-price-promo-duration {
        color: $gray3;
      }
      // stylelint-enable selector-max-class
    }

    &:active {
      color: $white;
      background-color: $black;
      border-color: $black;
    }

    &:active .price .price-amount, // deprecated
    &:active .ob1-price .ob1-price-amount,
    &:active .price .price-amount-inverse, // deprecated
    &:active .ob1-price .ob1-price-amount-inverse {
      color: $primary2;
    }

    &:active .ob1-price .ob1-price-by-month,
    &:active .ob1-price .ob1-price-from {
      color: $white;
    }

    &:active .ob1-price .ob1-price-promo-duration {
      color: $gray4;
    }
  }

  &.selected, // deprecated
  &.ob1-offer-tile-selected {
    border-color: $primary;

    &.focus-within:not(:focus-visible):not(:hover) {
      border-color: $primary;
    }

    &:focus-within:not(:focus-visible):not(:hover) {
      border-color: $primary;
    }
  }

  &.disabled, // deprecated
  &.ob1-offer-tile-disabled {
    color: $white !important; // stylelint-disable-line declaration-no-important
    pointer-events: none;
    background-color: $gray4 !important; // stylelint-disable-line declaration-no-important
    border-color: $gray4 !important; // stylelint-disable-line declaration-no-important

    .price-amount, // deprecated
    .ob1-price-amount,
    .price-amount-inverse, // deprecated
    .ob1-price-amount-inverse {
      color: $white !important; // stylelint-disable-line declaration-no-important
    }

    .ob1-price-by-month,
    .ob1-price-from {
      color: $black;
    }
    .ob1-price-promo-duration  {
      color: $gray3;
    }
  }

  // Permet de positionner par défaut le tag en haut à gauche (par dessus la bordure de la tuile)
  .tag, // deprecated
  .ob1-tag {
    position: absolute;
    top: px-to-rem(-1);
    left: px-to-rem(-1);
  }

  a {
    color: currentcolor;

    &:hover,
    &:active,
    & {
      text-decoration: none;
    }

    // Ne peut pas être mis au même niveau que le &:hover car :focus-within n'est pas reconnu par IE et donc le style du hover est ignoré également
    &:focus-within {
      text-decoration: none;
    }
  }

  &.focus-within a {
    text-decoration: none;
  }
}
