.dropdown {
  button:focus {
    outline: px-to-rem(2) solid $primary;
    outline-offset: 1px;
  }
}

.dropdown-menu {
  &[x-placement="top-start"],
  &[x-placement="bottom-start"] {
    border-color: $primary;
  }
}

.dropdown-item {
  &:hover,
  &:focus,
  &:hover:focus {
    color: $primary;
  }

  &:active,
  &:hover:focus:active {
    color: $black;
  }

  &.active {
    &:not(:hover):not(:focus):not(:active) {
      color: $primary;
    }

    &:hover,
    &:focus {
      color: $black;
    }
  }
}

.dropdown-header {
  color: $black;
}

.ob1-dropdown-item-checkbox {
  &:hover .custom-control-input:not(:disabled) ~ .custom-control-label,
  &:focus .custom-control-input:not(:disabled) ~ .custom-control-label {
    color: $black;
  }
}
