.ob1-filterchip {
  .ob1-filterchip-label {
    border-color: $black;

    &::before {
      font-family: icon-sosh, sans-serif;
    }
  }

  .ob1-filterchip-input {
    &:hover,
    &:focus {
      ~ .ob1-filterchip-label {
        padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;
        color: $black;
        border-color: $black;
        border-width: px-to-rem(2);
      }
    }

    &:focus:not(:focus-visible):not(:hover) ~ .ob1-filterchip-label {
      padding: $filterchip-padding-top $filterchip-padding-x $filterchip-padding-bottom;
      border-color: $black;
      border-width: px-to-rem(1);
    }

    &:focus ~ .ob1-filterchip-label {
      outline-color: $primary;
    }

    &:active ~ .ob1-filterchip-label {
      color: $white;
      background-color: $black;
    }

    &:checked {
      ~ .ob1-filterchip-label {
        padding: $filterchip-selected-padding-top $filterchip-selected-padding-x $filterchip-selected-padding-bottom;
        color: $white;
        background-color: $primary;
        border-color: $primary;

        &::before {
          color: $white;
        }
      }

      &:hover,
      &:focus {
        ~ .ob1-filterchip-label {
          background-color: $black;
          border-color: $black;
        }
      }

      &:focus:not(:focus-visible):not(:hover) {
        ~ .ob1-filterchip-label {
          padding: $filterchip-selected-padding-top $filterchip-selected-padding-x $filterchip-selected-padding-bottom;
          color: $white;
          background-color: $primary;
          border-color: $primary;
          border-width: px-to-rem(2);

          &::before {
            color: $white;
          }
        }
      }

      &:active ~ .ob1-filterchip-label {
        color: $black;
        background-color: transparent;
        border-color: $black;
        border-width: px-to-rem(2);

        &::before {
          color: $black;
        }
      }
    }

    &:disabled {
      ~ .ob1-filterchip-label {
        padding: $filterchip-padding-top $filterchip-padding-x $filterchip-padding-bottom;
        color: $gray4;
        background-color: transparent;
        border-color: $gray4;
        border-width: px-to-rem(1);
      }

      &:checked {
        ~ .ob1-filterchip-label {
          padding: $filterchip-selected-padding-top $filterchip-selected-padding-x $filterchip-selected-padding-bottom;
          color: $white;
          background-color: $gray4;
          border-color: $gray4;
          border-width: px-to-rem(1);

          &::before {
            color: $white;
          }
        }
      }
    }
  }

  &.ob1-filterchip-selected .ob1-filterchip-label {
    padding: $filterchip-selected-padding-top $filterchip-selected-padding-x $filterchip-selected-padding-bottom;
    color: $white;
    background-color: $primary;
    border-color: $primary;

    &::before {
      color: $white;
    }

    &:hover {
      background-color: $black;
      border-color: $black;
    }

    &:active {
      color: $black;
      background-color: transparent;
      border-color: $black;
      border-width: px-to-rem(2);

      &::before {
        color: $black;
      }
    }
  }

  &.ob1-filterchip-reverse {
    .ob1-filterchip-label {
      color: $white;
      border-color: $white;
    }

    .ob1-filterchip-input {
      &:hover,
      &:focus {
        ~ .ob1-filterchip-label {
          color: $white;
          border-color: $white;
          outline-color: $primary2;
        }
      }

      &:focus:not(:focus-visible):not(:hover) ~ .ob1-filterchip-label {
        border-color: $white;
      }

      &:active {
        ~ .ob1-filterchip-label {
          color: $black;
          background-color: $white;
        }
      }

      &:checked {
        ~ .ob1-filterchip-label {
          color: $black;
          background-color: $primary2;
          border-color: $primary2;

          &::before {
            color: $black;
          }
        }

        &:hover,
        &:focus {
          ~ .ob1-filterchip-label {
            background-color: $white;
            border-color: $white;
          }
        }

        &:focus:not(:focus-visible):not(:hover) {
          ~ .ob1-filterchip-label {
            color: $black;
            background-color: $primary2;
            border-color: $primary2;

            &::before {
              color: $black;
            }
          }
        }

        &:active {
          ~ .ob1-filterchip-label {
            color: $white;
            background-color: transparent;

            &::before {
              color: $white;
            }
          }
        }
      }

      &:disabled {
        ~ .ob1-filterchip-label {
          color: $gray3;
          background-color: transparent;
          border-color: $gray3;

          &::before {
            color: $white;
          }
        }

        &:checked {
          ~ .ob1-filterchip-label {
            color: $black;
            background-color: $gray3;
            border-color: $gray3;

            &::before {
              color: $black;
            }
          }
        }
      }
    }

    &.ob1-filterchip-selected .ob1-filterchip-label {
      color: $black;
      background-color: $primary2;
      border-color: $primary2;

      &::before {
        color: $black;
      }

      &:hover {
        background-color: $white;
        border-color: $white;
      }

      &:active {
        color: $white;
        background-color: transparent;

        &::before {
          color: $white;
        }
      }
    }
  }

  &.ob1-choicechip {
    .ob1-filterchip-input {
      &:checked {
        ~ .ob1-filterchip-label {
          padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;
        }

        &:focus:not(:focus-visible):not(:hover) ~ .ob1-filterchip-label {
          padding-right: $filterchip-active-padding-x;
          padding-left: $filterchip-active-padding-x;
        }

        &:active {
          ~ .ob1-filterchip-label {
            color: $white;
            background-color: $black;
            border-color: $black;
          }
        }

        &:disabled {
          ~ .ob1-filterchip-label {
            background-color: $gray4;
            border-color: $gray4;
          }
        }
      }
    }

    &.ob1-filterchip-selected .ob1-filterchip-label {
      padding: $filterchip-selected-padding-top $filterchip-active-padding-x $filterchip-selected-padding-bottom;

      &:active {
        color: $white;
        background-color: $black;
        border-color: $black;
      }
    }

    // stylelint-disable selector-max-class
    &.ob1-filterchip-reverse {
      .ob1-filterchip-input {
        &:active {
          ~ .ob1-filterchip-label {
            color: $black;
            background-color: $white;
            border-color: $white;
          }
        }

        &:disabled {
          ~ .ob1-filterchip-label {
            color: $gray3;
            background-color: transparent;
            border-color: $gray3;
          }
        }

        &:checked {
          &:disabled {
            ~ .ob1-filterchip-label {
              color: $black;
              background-color: $gray3;
              border-color: $gray3;
            }
          }
        }
      }

      &.ob1-filterchip-selected .ob1-filterchip-label {
        &:active {
          color: $black;
          background-color: $white;
          border-color: $white;
        }
      }
    }
    // stylelint-enable selector-max-class
  }

  &.ob1-inputchip {
    .ob1-filterchip-label {
      &:focus-visible {
        outline-color: $primary-official;
      }

      &:hover,
      &:focus,
      &:focus-visible {
        .icon-delete {
          color: $primary-official;
        }
      }

      &:active {
        .icon-delete {
          color: $black;
        }
      }
    }
  }

  &.ob1-inputchip-inverse {
    .ob1-filterchip-label {
      &:focus-visible {
        outline-color: $primary2;
      }

      &:hover,
      &:focus,
      &:focus-visible {
        .icon-delete {
          color: $primary2;
        }
      }

      &:active {
        .icon-delete {
          color: $white;
        }
      }
    }
  }
}

.ob1-color-chips {
  .ob1-color-chip-input {
    &:focus + .ob1-color-chip-label {
      outline: px-to-rem(1) dotted $primary; // TODO : il faudra mettre à jour cette propriété lorsqu'OB1 héritera de la version de Boosted ayant un outline à "2px solid black"
    }

    &:checked + .ob1-color-chip-label {
      border-color: $primary;
    }
  }
}
