$floating-btn-icon-size: px-to-rem(56);
$open-floating-btn-size: px-to-rem(201);

.ob1-floating-action-btn {
  position: relative;
  width: $floating-btn-icon-size;
  height: $floating-btn-icon-size;
  overflow: hidden;
  font-size: $font-size-transverse2;
  font-weight: $font-weight-bold;
  line-height: $line-height-transverse2;
  background-color: $white;
  border: none;
  border-radius: $floating-btn-icon-size;
  outline-offset: px-to-rem(4);
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .4);
  transition: outline-offset .15s ease-in-out, width 200ms ease-in;

  animation-name: ob1-floating-action-btn-open, ob1-floating-action-btn-close;
  animation-duration: 5.52s, 200ms;
  animation-timing-function: ease-out, ease-in;
  animation-delay: 5s, 10.52s;

  .ob1-floating-action-btn-content {
    position: absolute;
    top: px-to-rem(7);
    left: px-to-rem(20);
    width: px-to-rem(110);
    text-align: left;
  }

  &::after {
    position: absolute;
    top: px-to-rem(-11);
    right: 0;
    font-family: icon-orange, sans-serif;
    font-size: $floating-btn-icon-size;
    color: $primary;
    content: $icon-Djingo;
    background-color: $white;
  }

  &:focus {
    outline: px-to-rem(2) solid $primary;
    outline-offset: 0;
  }

  &:hover,
  &:focus {
    &::after {
      color: $gray3;
    }
  }

  &:focus:not(:focus-visible):not(:hover) {
    &::after {
      color: $primary;
    }
  }

  &:active {
    &::after {
      color: $primary;
    }
  }
}

@include media-breakpoint-up(md) {
  .ob1-floating-action-btn {
    &:hover,
    &:focus {
      width: $open-floating-btn-size;
      transition: width 520ms ease-out;
    }

    &:focus:not(:focus-visible):not(:hover) {
      width: $floating-btn-icon-size;
      padding: 0;
    }
  }
}

@keyframes ob1-floating-action-btn-open {
  0% {
    width: $floating-btn-icon-size;
  }
  10% {
    width: $open-floating-btn-size;
  }
  100% {
    width: $open-floating-btn-size;
  }
}

@keyframes ob1-floating-action-btn-close {
  0% {
    width: $open-floating-btn-size;
  }
  100% {
    width: $floating-btn-icon-size;
  }
}
