.ob1-carrousel {
  .swiper-slide {
    &:focus {
      outline-color: $primary;
    }

    >a {
      width: 100%;
    }
  }

  .swiper-button-next,
  .swiper-button-prev,
  .ob1-carrousel-controls {
    display: inline-flex;
    background-color: transparent;

    &::before {
      font-family: icon-sosh, sans-serif;
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    top: calc(50% - 22px);

    &::before {
      font-size: px-to-rem(14);
      text-shadow: 0 px-to-rem(2) $black;
    }

    &:hover,
    &:active,
    &:focus {
      background: rgba(0, 0, 0, .54);
      border-radius: 3px;
    }
  }

  .swiper-button-next {
    padding: 6px 10px 7px 12px;
  }

  .swiper-button-prev {
    padding: 6px 12px 7px 10px;
  }

  .ob1-carrousel-controls {
    bottom: 14px;
    left: 15px;
    padding: 0;
    color: $black;

    .icon {
      font-size: px-to-rem(18);
    }

    .ob1-link-icon-text {
      display: inline;
    }

    &:active {
      color: $primary;
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $black;
    }
  }

  .swiper-pagination {
    position: static;
    display: flex;
    justify-content: center;
    padding: 0 15px;
    margin: 8px 0;
    background-color: $white;
    transform: none;
  }

  &.ob1-carrousel-xs-ysidebar {
    .ob1-carrousel-sidebar {
      max-height: none;
    }
  }

  &.ob1-carrousel-xsidebar {
    .ob1-carrousel-sidebar {
      left: 46px;
      width: calc(50% - 56px);
    }
  }

  @include media-breakpoint-up(sm) {
    &.ob1-carrousel-xsidebar {
      .ob1-carrousel-img {
        width: 50%;
        max-width: none;
      }

      .ob1-carrousel-sidebar {
        width: 50%;
        max-width: none;
      }
    }

    &.ob1-carrousel-xs-ysidebar {
      .ob1-carrousel-img {
        width: 50%;
        min-width: auto;
      }

      .ob1-carrousel-sidebar {
        left: 46px;
        width: calc(50% - 56px);
        max-width: none;
      }
    }

    .ob1-slide-inversion {
      text-align: left;

      .ob1-carrousel-sidebar {
        right: 46px;
        left: auto;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .swiper-button-prev,
    .swiper-button-next {
      &::before {
        font-size: px-to-rem(16);
      }
    }

    .swiper-button-prev {
      padding: 11px 16px 11px 14px;
    }

    .swiper-button-next {
      padding: 11px 14px 11px 16px;
    }

    .ob1-carrousel-controls {
      left: 27px;
    }

    .swiper-pagination {
      padding: 0 27px;
    }

    &.ob1-carrousel-xsidebar,
    &.ob1-carrousel-xs-ysidebar {
      .ob1-carrousel-img {
        min-width: auto;
      }

      .ob1-carrousel-sidebar {
        left: 56px;
        width: calc(50% - 66px);
      }
    }

    .ob1-slide-inversion {
      .ob1-carrousel-sidebar {
        right: 56px;
        left: auto;
      }
    }
  }

  @include media-breakpoint-up(xl) {
    &.ob1-carrousel-xsidebar,
    &.ob1-carrousel-xs-ysidebar {
      .ob1-carrousel-img {
        min-width: auto;
      }

      .ob1-carrousel-sidebar {
        left: 61px;
        width: calc(50% - 76px);
      }
    }

    .ob1-slide-inversion {
      .ob1-carrousel-sidebar {
        right: 61px;
        left: auto;
      }
    }
  }
}

.ob1-carrousel-product-sheet {
  .swiper-pagination {
    display: inline-flex;
  }
}
