.ob1-sub-menu-page {
  display: none;
}

.ob1-sub-menu-dropdown {
  display: block;
  .o-link-arrow {
    max-width: none;
    padding: 23px 0 10px;

    &::after {
      position: absolute;
      right: px-to-rem(15);
    }
  }
  .btn,
  .dropdown-menu {
    width: 100%;
  }
}

@include media-breakpoint-up(md) {
  .ob1-sub-menu-page {
    display: block;
    border-bottom: 1px solid $gray4;
    .sub-nav {
      display: flex;
      padding: 15px 0 20px;
      margin-bottom: 0;
      list-style: none;

      .sub-nav-link {
        margin: 0 45px 0 0;
        font-size: $lead-font-size-widescreen;
        font-weight: $font-weight-normal;
      }

      .active {
        .sub-nav-link {
          font-weight: $font-weight-bold;
        }
      }
    }
  }

  .ob1-sub-menu-dropdown {
    display: none;
  }
}
