.ob1-card {
  border-width: px-to-rem(2);
  border-radius: px-to-rem(3);
  box-shadow: unset;

  &:hover,
  &.focus-within {
    border-color: $black;
    box-shadow: unset;
  }

  &:focus-within {
    border-color: $black;
    box-shadow: unset;
  }

  &.focus-within:not(:focus-visible):not(:hover) {
    border-color: $gray4;
    box-shadow: unset;
  }

  &:focus-within:not(:focus-visible):not(:hover) {
    border-color: $gray4;
    box-shadow: unset;
  }

  &:active {
    border-color: $primary;
    box-shadow: unset;
  }

  &.ob1-card-selected {
    border-color: $primary;
    box-shadow: unset;

    &:hover,
    &.focus-within,
    &:active {
      border-color: $black;
      box-shadow: unset;
    }

    &:focus-within {
      border-color: $black;
      box-shadow: unset;
    }

    &.focus-within:not(:focus-visible):not(:hover) {
      border-color: $primary;
    }

    &:focus-within:not(:focus-visible):not(:hover) {
      border-color: $primary;
    }
  }

  &.ob1-card-inverse {
    &:hover,
    &.focus-within {
      border-color: $white;
    }

    &:focus-within {
      border-color: $white;
    }

    &:active {
      border-color: $primary2;
    }

    &.ob1-card-selected {
      border-color: $primary2;

      &:hover,
      &.focus-within,
      &:active {
        border-color: $white;
      }

      &:focus-within {
        border-color: $white;
      }

      &.focus-within:not(:focus-visible):not(:hover) {
        border-color: $primary2;
      }

      &:focus-within:not(:focus-visible):not(:hover) {
        border-color: $primary2;
      }
    }
  }

  &.ob1-card-link {
    &::after {
      font-family: icon-sosh, sans-serif;
      color: $primary;
    }
  }
}

a.ob1-card { // stylelint-disable-line selector-no-qualifying-type
  &:hover,
  &:focus {
    color: $black;
  }
}
