.custom-control {
  .custom-control-label {
    &:active::before,
    &::before {
      font-family: icon-sosh, sans-serif;
    }
  }

  .custom-control-input {
    &:focus ~ .custom-control-label::before {
      outline-color: $primary;
    }
  }
}

.bg-dark,
.bg-gray1 {
  .custom-control {
    .custom-control-input {
      &:focus ~ .custom-control-label::before {
        outline-color: $primary2;
      }
    }
  }
}

/* $greige variable value changes in Sosh (gray2) */
.bg-greige {
  background-color: $greige !important; // stylelint-disable-line declaration-no-important
}
