.ob1-social-share-bar {

  .ob1-social-share-list {
    margin-bottom: 39px;

    ul {
      display: flex;
      padding-left: 0;
      list-style-type: none;

      .ob1-social-share-network {
        margin-right: 15px;
      }

      @include media-breakpoint-up(md) {
        .ob1-social-share-network {
          margin-right: 24px;
        }
      }

      img {
        width: px-to-rem(39);
      }

      @include media-breakpoint-up(md) {
        img {
          width: px-to-rem(60);
        }
      }
    }
  }

  .ob1-social-share-copy-bar {

    &:focus-within {
      outline: .125rem solid $primary;
    }
    &.focus-within {
      outline: .125rem solid $primary;
    }

    display: flex;
    flex-wrap: wrap;
    @include media-breakpoint-up(md) {
      flex-wrap: nowrap;
    }
    padding: 10px 15px 12px;
    margin: 0 -15px;
    @extend .composite-link;
    background-color: $gray2;

    .ob1-social-share-link-icon {
      display: flex;
      align-self: center;
      margin-left: auto;
      font-weight: $font-weight-bold;
      background: none;
      border: none;
      @extend .lead;

      .icon-copy {
        align-self: center;
        width: px-to-rem(20);
        height: px-to-rem(20);
        padding-top: 1px;
      }
      &:hover,
      &:focus {
        color: $gray3;
      }
      &:focus {
        outline: none;
      }
    }

    .ob1-social-share-copy-url {
      padding-top: 8px;
      margin-bottom: 0;
      @extend .lead;
      color: $gray3;
      word-wrap: break-word;
      @include media-breakpoint-up(md) {
        display: inline-flex;
        align-items: center;
        font-weight: $font-weight-bold;
        color: $dark;
      }
    }
  }
}
