@mixin shadow-position($shadow-position, $margin-value) {
  $opposite-position: map_get($position-and-opposite, $shadow-position);
  .card-header {
    margin-#{$shadow-position}: $margin-value;
    margin-#{$opposite-position}: 0;
  }

  .card-img-top {
    @if $shadow-position == left {
      margin-left: -#{$margin-value};
    } @else {
      margin-left: $margin-value;
    }
  }
}

@mixin on-hover-n-focus($bg-color, $bg-color-inverse) {
  color: $bg-color-inverse;
  background-color: $bg-color;

  a {
    color: $bg-color-inverse;
  }
}

@mixin on-hover-n-focus-visible($bg-color, $bg-color-inverse, $bg-color-init: $bg-color-inverse) {
  &.focus-within,
  &:hover {
    @include on-hover-n-focus($bg-color, $bg-color-inverse);
  }

  &:focus-within {
    @include on-hover-n-focus($bg-color, $bg-color-inverse);
  }

  &.focus-within:not(:focus-visible):not(:hover) {
    @include on-hover-n-focus($bg-color-init, $bg-color);
  }

  &:focus-within:not(:focus-visible):not(:hover) {
    @include on-hover-n-focus($bg-color-init, $bg-color);
  }
}

@mixin sosh-push-colors($bg-color, $highlight-color) {
  .card-header {
    a:focus {
      outline: none;
    }

    .card-label-lead,
    .card-label,
    .card-text-light,
    .card-text {
      background-color: $bg-color;
    }

    .card-text-light::after {
      color: $highlight-color;
    }

    &.focus-within,
    &:hover {
      .card-label-lead,
      .card-label,
      .card-text-light,
      .card-text {
        border-color: $highlight-color;
      }
    }

    &:focus-within {
      .card-label-lead,
      .card-label,
      .card-text-light,
      .card-text {
        border-color: $highlight-color;
      }
    }
  }
}
