.btn {
  padding: $btn-padding-y $btn-padding-x;
  border-width: $btn-border-width;
  border-radius: $btn-border-radius;

  &:focus {
    outline-color: $primary;
  }

  &[data-ob1-component="ProgressBarDetermined"] {
    .ob1-progress-bar-determined {
      top: -1px;
    }

    &.btn-inverse {
      .ob1-progress-bar-determined-inner {
        background-color: $primary2;
      }
    }
  }
}

.bg-smagenta1 .btn,
.bg-sblue1 .btn,
.btn.btn-inverse {
  &:focus {
    outline-color: $primary2;
  }
}

.btn-primary,
.btn-tertiary,
.btn-warning {
  @include ob1-outline-button-variant($smagenta1, $smagenta1, $black, $black, transparent, $smagenta1, $sgray4, $sgray4, $white, $white, $white);

  &.btn-inverse {
    @include ob1-outline-button-variant($syellow1, $syellow1, $white, $white, transparent, $syellow1, $sgray3, $sgray3, $black, $black, $black);
  }
}

.btn-primary.btn-bg-light {
  @include ob1-outline-button-variant($black, $black, $smagenta1, $smagenta1, transparent, $smagenta1, $sgray4, $sgray4, $white, $white, $white, $smagenta1);
}

.bg-syellow1 .btn-dark, // .btn-dark deprecated
.bg-syellow1 .btn-primary {
  @include ob1-outline-button-variant($black, $black, $white, $white, transparent, $black, transparent, transparent, transparent, $syellow1, $black);
}

.bg-smagenta1 .btn-dark, // .btn-dark deprecated
.bg-smagenta1 .btn-primary {
  @include ob1-outline-button-variant($white, $white, $black, $black, transparent, $white, transparent, transparent, transparent, $smagenta1, $white);
}

.bg-sblue1 .btn-dark, // .btn-dark deprecated
.bg-sblue1 .btn-primary {
  @include ob1-outline-button-variant($white, $white, $black, $black, transparent, $white, transparent, transparent, transparent, $sblue1, $white);
}

.btn-secondary,
.btn-light {
  @include ob1-outline-button-variant(transparent, $smagenta1, transparent, $black, $smagenta1, $smagenta1, transparent, $sgray4, $gray4, $smagenta1, $black, $white);

  &.btn-inverse {
    @include ob1-outline-button-variant(transparent, $syellow1, transparent, $white, $syellow1, $syellow1, transparent, $gray3, $gray3, $syellow1, $white, $black);
  }
}

.bg-syellow1 .btn-secondary {
  @include ob1-outline-button-variant(transparent, $black, $white, $white, $black, $black, transparent, transparent, transparent, $black, $black, $syellow1);
}

.bg-smagenta1 .btn-secondary {
  @include ob1-outline-button-variant(transparent, $white, $black, $black, $white, $white, transparent, transparent, transparent, $white, $white, $smagenta1);
}

.bg-sblue1 .btn-secondary {
  @include ob1-outline-button-variant(transparent, $white, $black, $black, $white, $white, transparent, transparent, transparent, $white, $white, $sblue1);
}

.bg-gray2,
.bg-greige,
.ob1-commercial-push {
  .btn-secondary {
    @include ob1-outline-button-variant(transparent, $black, transparent, $smagenta1, $smagenta1, $smagenta1, transparent, $sgray4, $gray4, $black, $smagenta1, $white);
  }
}
