.ob1-switch-view {
  $switch-view-border-radius: px-to-rem(15);

  border-radius: $switch-view-border-radius;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .5);

  .btn-check {
    position: absolute;
    clip: rect(0, 0, 0, 0);
    pointer-events: none;

    + .btn {
      padding: px-to-rem(3) px-to-rem(10) px-to-rem(5);
      margin: 0;
      font-size: $font-size-transverse2;
      line-height: $line-height-transverse2;
      border: none;
      border-radius: 0;
      outline-offset: px-to-rem(4);
      transition: outline-offset .15s ease-in-out;

      &:first-of-type {
        padding-left: px-to-rem(15);
        border-top-left-radius: $switch-view-border-radius;
        border-bottom-left-radius: $switch-view-border-radius;
      }

      &:last-of-type {
        padding-right: px-to-rem(15);
        border-top-right-radius: $switch-view-border-radius;
        border-bottom-right-radius: $switch-view-border-radius;
      }

      &:hover,
      &:focus {
        color: $white;
        cursor: pointer;
        background-color: $gray3;
      }

      &:focus:not(:focus-visible):not(:hover) {
        color: $black;
        cursor: auto;
        background-color: $white;
      }

      &:active {
        color: $white;
        cursor: pointer;
        background-color: $primary;
      }
    }

    &:focus + .btn {
      outline: px-to-rem(2) solid $primary;
      outline-offset: 0;
    }

    &:checked {
      + .btn {
        color: $white;
        background-color: $black;

        &:hover,
        &:active {
          background-color: $gray3;
        }
      }

      &:focus-visible + .btn {
        background-color: $gray3;
      }
    }
  }
}
