.ob1-menu-page {
  .o-nav-light > .nav-item {
    .nav-link::before {
      border-color: $primary;
    }

    &.active {
      .nav-link { // stylelint-disable-line selector-max-class
        color: $primary;
      }

      // stylelint-disable selector-max-class
      &:hover .nav-link,
      &:focus .nav-link,
      &.focus-within .nav-link {
        // stylelint-enable selector-max-class
        color: $black;
      }

      // stylelint-disable selector-max-class
      &:focus-within .nav-link {
        // stylelint-enable selector-max-class
        color: $black;
      }
    }

    &:not(.active):active .nav-link:not(.disabled):not(.o-link-arrow) {
      color: $black;
    }
  }
}

.ob1-menu-page-inverse {
  .o-nav-light > .nav-item {
    .nav-link::before {
      border-color: $primary2;
    }

    &.active {
      .nav-link { // stylelint-disable-line selector-max-class
        color: $white;
      }

      &:hover .nav-link { // stylelint-disable-line selector-max-class
        color: $primary2;
      }

      // stylelint-disable selector-max-class
      &:focus .nav-link,
      &.focus-within .nav-link {
        // stylelint-enable selector-max-class
        color: $white;
      }

      // stylelint-disable selector-max-class
      &:focus-within .nav-link {
        // stylelint-enable selector-max-class
        color: $white;
      }
    }
  }

  .o-nav-light > .nav-item > .nav-link {
    &:hover {
      color: $primary2;
    }

    &:focus {
      outline-color: $primary2;
    }
  }
}

.ob1-menu-page-dropdown {
  .o-link-arrow {
    &:hover,
    &:focus,
    &:active {
      color: $primary;
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $primary;
    }

    &::after {
      color: currentcolor;
    }
  }

  &.active {
    .o-link-arrow {
      color: $primary;

      &:hover,
      &:focus,
      &:active {
        color: $black;
      }
    }
  }
}
