.list {
  li::before {
    background-color: $primary;
  }

  &.ob1-list-tick li::before {
    color: $primary;
    background-color: transparent;
  }
}

.bg-dark .list {
  li::before {
    background-color: $primary2;
  }

  &.ob1-list-tick li::before {
    color: $primary2;
    background-color: transparent;
  }
}

.bg-success-light,
.bg-warning-light,
.bg-danger-light,
.bg-info-light {
  .list li::before {
    background-color: $black;
  }

  .ob1-list-tick li::before {
    color: $black;
    background-color: transparent;
  }
}
