.o-link-arrow {
  &::after {
    font-family: icon-sosh, sans-serif;
  }

  &.back {
    &::before {
      font-family: icon-sosh, sans-serif;
    }
  }
}

.bg-gray2 {
  .o-link-arrow {
    color: $black;

    &:active {
      color: $primary;
    }

    &:focus:not(:focus-visible):not(:hover) {
      color: $black;
    }
  }
}
