.ob1-box-radio {
  padding-bottom: px-to-rem(1);
  border-width: px-to-rem(2);
  border-bottom-width: px-to-rem(1);
  border-radius: px-to-rem(3);

  &:hover,
  &:active {
    padding-bottom: 0;
    border-color: $black;
    border-bottom-width: px-to-rem(2);
  }
}

.ob1-box-radio-selected {
  padding-bottom: 0;
  border-color: $primary;
  border-bottom-width: px-to-rem(2);
}
