.ob1-page-indicator {
  .ob1-page-indicator-item {
    width: px-to-rem(16);
    padding-bottom: 7px;
    margin-right: 20px;

    &:nth-last-child(n+3),
    &:nth-last-child(n+3) ~ .ob1-page-indicator-item,
    &:nth-last-child(n+4),
    &:nth-last-child(n+4) ~ .ob1-page-indicator-item,
    &:nth-last-child(n+5),
    &:nth-last-child(n+5) ~ .ob1-page-indicator-item {
      width: px-to-rem(16);
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }
    }

    &:last-child {
      margin-right: 0;
    }

    .ob1-page-indicator-button {
      height: px-to-rem(16);
      border-radius: 50%;
      transition: none;

      &:hover,
      &:active {
        background-color: transparent;
        border-color: transparent transparent $dark;
        border-style: solid;
        border-width: 0 px-to-rem(18 / 2) px-to-rem(16);
        border-radius: 0;
        transition: none;
      }

      &:active {
        border-color: transparent transparent $primary;
      }
    }

    &.ob1-page-indicator-active{
      width: px-to-rem(18) !important; // stylelint-disable-line declaration-no-important

      .ob1-page-indicator-button {
        background-color: transparent;
        border-color: transparent transparent $primary;
        border-style: solid;
        border-width: 0 px-to-rem(18 / 2) px-to-rem(16);
        border-radius: 0;
      }
    }
  }

  &.ob1-page-indicator-inverse {
    .ob1-page-indicator-button {
      &:hover {
        border-color: transparent transparent $white;
      }

      &:active {
        border-color: transparent transparent $primary2;
      }
    }

    .ob1-page-indicator-active {
      .ob1-page-indicator-button {
        border-color: transparent transparent $primary2;
      }
    }
  }

  &.ob1-page-indicator-neutral {
    .ob1-page-indicator-button {
      background-color: $gray4;

      &:hover {
        border-color: transparent transparent $gray4;
      }

      &:active {
        border-color: transparent transparent $primary;
      }
    }

    .ob1-page-indicator-active {
      .ob1-page-indicator-button {
        background-color: transparent;
        border-color: transparent transparent $primary;
      }
    }
  }
}
